import Constants from "./../../utils/Constants";

const { ACTIONS } = Constants;

let initialState = {
  isLoginLoading: false, // Used to show loading when the login button submit.
};

export const loginState = (state = initialState, action) => {
  const { type ,loginUserData} = action;

  switch (type) {
    case ACTIONS.SHOW_LOGIN_LOADING:
      return { ...state, isLoginLoading: true };
    case ACTIONS.HIDE_LOGIN_LOADING:
      return { ...state, isLoginLoading: false };
    case ACTIONS.LOGIN_SUCCESS:
      return { ...state, loginUserData };
    case ACTIONS.LOGOUT_CLEAR_ALL_STATES:
      return { ...initialState };
    default:
      return state;
  }
};
