let baseUrl = "";
let baseUrlSocketIO = "";
let baseUrlSocketIOPath = "";
let s3ImageUrl = "";
let s3VideoUrl = "";
let serverName = "";
let isDevelopment = true;

if (
  window.location.hostname === "localhost" ||
  window.location.hostname === "webapp-dev.embassysocial.io"
) {
  baseUrl = "https://devapi.embassysocial.io/";
  baseUrlSocketIO = "https://devsocket.embassysocial.io/";
  baseUrlSocketIOPath = "";
  s3ImageUrl = "https://dev-media.embassysocial.io/";
  s3VideoUrl = "https://dev-video.embassysocial.io/";
  serverName = "Dev Server";
} else if (window.location.hostname === "webapp-qa.embassysocial.io") {
  baseUrl = "https://qaapi.embassysocial.io/";
  baseUrlSocketIO = "https://qasocket.embassysocial.io/";
  baseUrlSocketIOPath = "";
  s3ImageUrl = "https://stage-media.embassysocial.io/";
  s3VideoUrl = "https://stage-video.embassysocial.io/";
  serverName = "Qa Server";
} else if (window.location.hostname === "webapp-stage.embassysocial.io") {
  baseUrl = "https://stageapi.embassysocial.io/";
  baseUrlSocketIO = "https://stagesocket.embassysocial.io/";
  baseUrlSocketIOPath = "";
  s3ImageUrl = "https://stage-media.embassysocial.io/";
  s3VideoUrl = "https://stage-video.embassysocial.io/";
  serverName = "Stage Server";
} else {
  baseUrl = "https://api.embassysocial.io/";
  baseUrlSocketIO = "https://socket.embassysocial.io/";
  baseUrlSocketIOPath = "";
  s3ImageUrl = "https://image.embassysocial.io/";
  s3VideoUrl = "https://video.embassysocial.io/";
  serverName = "Prod Server";
  isDevelopment = false;
}

export const BASE_URL = baseUrl;
export const BASE_URL_SOCKET_IO = baseUrlSocketIO;
export const BASE_URL_SOCKET_IO_PATH = baseUrlSocketIOPath;
export const S3_GET_IMAGE_URL = s3ImageUrl;
export const S3_GET_VIDEO_URL = s3VideoUrl;
export const SERVER_NAME = serverName;
export const IS_DEVELOPMENT = isDevelopment;

