import Constants from "./../../utils/Constants";
import HttpBaseClient from "./../../network/HttpBaseClient";
import { REGISTER_NEW_USER, GET_INTERESTS } from "../../utils/URL";
import { handleError } from "./NetworkAction";
import Toaster from "./../../utils/Toaster/Toast";
import { callRefreshTokenApi } from "./LoginAction";

export const registerUserDetails = (
  aboutYouObject,
  interests,
  user_role_id,
  user_role,
  history
) => {
  return (dispatch, getState) => {
    //   dispatch(showInterestLoading());
    HttpBaseClient.post(
      REGISTER_NEW_USER,
      {
        ...aboutYouObject,
        interests: interests,
        user_role_id,
        user_role,
      },
      1
    )
      .then((response) => {
        // dispatch(hideInterestLoading());
        if (response.code === 0) {
          Toaster.success("User details & interest saved");
          // dispatch(callRefreshTokenApi());
          //   Actions.uploadShotsScreen();
          history.push("/upload-shots");
        } else {
          Toaster.error(response.message);
        }
      })
      .catch((error) => {
        // dispatch(hideInterestLoading());
        dispatch(handleError(error));
      });
  };
};

// get defaults interest to show in share your interests screen
export const getInterests = () => {
  return (dispatch, getState) => {
    dispatch(showInterestLoading());
    HttpBaseClient.get(GET_INTERESTS, {}, 2)
      .then((response) => {
        if (response.code === 0) {
          dispatch({
            type: Constants.ACTIONS.GET_USER_INTERESTS,
            interestArray: response.data,
          });
          dispatch(hideInterestLoading());
        }
      })
      .catch((error) => {
        dispatch(hideInterestLoading());
        dispatch(handleError(error));
      });
  };
};

export const showInterestLoading = () => {
  return (dispatch) => {
    dispatch({
      type: Constants.ACTIONS.SHOW_INTEREST_LOADING,
    });
  };
};

export const hideInterestLoading = () => {
  return (dispatch) => {
    dispatch({
      type: Constants.ACTIONS.HIDE_INTEREST_LOADING,
    });
  };
};

export const changeSelection = (
  index,
  selectionStatus,
  id,
  globalInterestArr
) => {
  return (dispatch, getState) => {
    let changeArray = globalInterestArr;
    for (let i = 0; i < changeArray.length; i++) {
      if (changeArray[i]._id == id) {
        changeArray[i].is_selected = selectionStatus;
      }
    }
    dispatch({
      type: Constants.ACTIONS.GET_USER_INTERESTS,
      interestArray: changeArray,
    });
  };
};
