import Constants from "./../../utils/Constants";

const { ACTIONS } = Constants;

let initialState = {
  signupUser: {},
  userProfileDetails: {},
  aboutUserDetails: {},
  isSingUpPageLoading: false,
};

export const signupState = (state = initialState, action) => {
  const { type, aboutUserDetails } = action;
  switch (type) {
    case ACTIONS.SET_SIGNUP_USER:
      return { ...state, signupUser: action.payload };
    case ACTIONS.SAVE_ABOUT_DETAILS_SUCCESS:
      return { ...state, aboutUserDetails };
    case ACTIONS.SHOW_SIGN_UP_SCREEN_LOADING:
      return { ...state, isSingUpPageLoading: true };
    case ACTIONS.HIDE_SIGN_UP_SCREEN_LOADING:
      return { ...state, isSingUpPageLoading: false };
    case ACTIONS.GET_USER_UPLOAD_PROFILE:
      return { ...state, userProfileDetails: action.payload };

    default:
      return state;
  }
};
