import React, { useEffect, useState } from "react";
import { connect, useSelector } from "react-redux";
import { useHistory } from "react-router-dom";
import { ProgressBar } from "react-bootstrap";
// Image
import arrowleft from "./../../assets/images/arrow-left.svg";
import arrowright from "./../../assets/images/arrow-right.svg";
import profileImg from "./../../assets/images/profile-vector.svg";
import bgImg from "./../../assets/images/cover-photo.png";
import profileWhite from "./../../assets/images/profile-white.svg";
import bgprofileImg from "./../../assets/images/bg-photo.svg";
import logo from "./../../assets/images/logo.svg";
import tick from "./../../assets/images/tick.svg";
// API Call
import { updateUserProfile } from "./../../redux/actions/SignupAction";
import { getProfileImageOriginalUrl } from "../../utils/Utility";
// Common Component
import Stepper from "../../components/Stepper";

export const UploadShotsScreen = (props) => {
  const [profileImage, setProfileImage] = useState("");
  const [backgroundImage, setBackgroundImage] = useState("");
  const [profileTick, setProfileTick] = useState(false);
  const [backgroundTick, setBackgroundTick] = useState(false);

  const history = useHistory();

  // From Reducer
  const signupState = useSelector((state) => state.signupState);
  const loginState = useSelector((state) => state.loginState);
  const deviceState = useSelector((state) => state.deviceState);

  // redirect to  conncet to social login
  const handleSubmit = () => {
    history.push("/add-bio");
  };

  // upload image to aws server
  const handleFileUpload = async (e) => {
    let isUpdateBackgroundImage = false;

    if (e.target.id === "bg_upload_file") {
      isUpdateBackgroundImage = true;
    } else {
      isUpdateBackgroundImage = false;
    }
    // call update user profile to get user details
    props.updateUserProfile(
      e.target.files,
      deviceState,
      isUpdateBackgroundImage
    );
  };

  useEffect(() => {
    if (signupState.userProfileDetails) {
      if (signupState.userProfileDetails.isProfileImageUploaded === true) {
        setProfileImage(signupState.userProfileDetails.photo);
        setProfileTick(true);
      }
      if (signupState.userProfileDetails.isBackgroundImageUploaded === true) {
        setBackgroundImage(signupState.userProfileDetails.background_photo);
        setBackgroundTick(true);
      }
    }
  }, [signupState.userProfileDetails]);

  useEffect(() => {
    if (loginState.loginUserData.user) {
      if (loginState.loginUserData.user.isProfileImageUploaded === true) {
        setProfileImage(loginState.loginUserData.user.photo);
        setProfileTick(true);
      }
      if (loginState.loginUserData.user.isBackgroundImageUploaded === true) {
        setBackgroundImage(loginState.loginUserData.user.background_photo);
        setBackgroundTick(true);
      }
    }
  }, [loginState.loginUserData.user]);

  return (
    <div className="w-100 h-100 position-fixed">
      <div className="w-100 h-100 d-flex overflow-auto">
        <div className="container-fluid">
          <div className="row h-100">
            <div className="col-sm-12">
              <div className="d-flex flex-column h-100">
                <div className="logo">
                  <img src={logo} className="mt-3" alt="" />
                </div>
                <div className="row h-100">
                  <div className="col-sm-6 pt-4 offset-sm-3 d-flex flex-column all-contents">
                    <p className="text-muted small fw-bold mb-1">4/7</p>
                    <Stepper stepperActive={3} />
                    <h1 className="mt-5 pb-5 display-3">
                      Let's add some shots!
                    </h1>
                    <div className="row">
                      <div className="col-5 m-auto">
                        <div className="card border-0">
                          <img
                            className="card-img"
                            src={
                              backgroundImage
                                ? getProfileImageOriginalUrl(
                                    backgroundImage,
                                    deviceState
                                  )
                                : bgImg
                            }
                            alt=""
                          />
                          <div className="card-img-overlay">
                            <div className="d-flex py-3 flex-fill bd-highlight flex-wrap">
                              <div className="me-3 profile-pic2">
                                <img
                                  className="w-100"
                                  src={
                                    profileImage
                                      ? getProfileImageOriginalUrl(
                                          profileImage,
                                          deviceState
                                        )
                                      : profileImg
                                  }
                                  alt=""
                                />
                              </div>
                              <div className="profile-msg align-self-center flex-grow-1 text-left">
                                <h4 className="m-0 text-white display-6">
                                  Maidson <br />
                                  Kelly
                                </h4>
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                    <div className="mt-5 signup-photos">
                      <div className="btn_upload mb-3">
                        <input
                          type="file"
                          id="upload_file"
                          name="profileUpload"
                          onChange={handleFileUpload}
                        />
                        <div className=" align-self-start pt-1 me-4">
                          <img
                            src={
                              profileTick ? tick : profileWhite
                              // userLoginDetailsData
                              //   ? userLoginDetailsData.user
                              //       .isProfileImageUploaded === true ||
                              //     userProfileDetailsData.isProfileImageUploaded ===
                              //       true
                              //     ? tick
                              //     : profileWhite
                              //   : profileWhite
                            }
                            alt=""
                            className="img-fluid"
                          />
                        </div>
                        <div className="profile-msg align-self-center flex-grow-1">
                          <h5 className="mb-0 text-white display-6">
                            Choose profile picture
                          </h5>
                          {profileTick ? (
                            ""
                          ) : (
                            <p className="mb-0 text-secondary">
                              We recommend your logo!
                            </p>
                          )}
                        </div>
                        <div className="list-inline align-self-center text-center">
                          <a href="" className="text-white">
                            <i className="far fa-chevron-right"></i>
                          </a>
                        </div>
                      </div>
                      {deviceState.isUploaded && deviceState.progress > 0 && (
                        <div>
                          <ProgressBar
                            striped
                            variant="success"
                            now={
                              deviceState.isUploaded ? deviceState.progress : 0
                            }
                            label={`${
                              deviceState.isUploaded ? deviceState.progress : 0
                            }%`}
                          />

                          <br></br>
                        </div>
                      )}
                      <div className="btn_upload mb-3">
                        <input
                          type="file"
                          id="bg_upload_file"
                          name="profileUpload"
                          onChange={handleFileUpload}
                        />
                        <div className="align-self-start pt-1 me-4">
                          <img
                            src={
                              backgroundTick ? tick : bgprofileImg
                              // userLoginDetailsData
                              //   ? userLoginDetailsData.user
                              //       .isBackgroundImageUploaded === true ||
                              //     userProfileDetailsData.isBackgroundImageUploaded ===
                              //       true
                              //     ? tick
                              //     : bgprofileImg
                              //   : bgprofileImg
                            }
                            alt=""
                            className="img-fluid"
                          />
                        </div>
                        <div className="profile-msg align-self-center flex-grow-1">
                          <h5 className="mb-0 text-white display-6">
                            Choose background photo
                          </h5>
                          {backgroundTick ? (
                            ""
                          ) : (
                            <p className="mb-0 text-secondary">
                              We recommend a full body or lifestyle shot!
                            </p>
                          )}
                        </div>
                        <div className="list-inline align-self-center text-center">
                          <a href="" className="text-white">
                            <i className="far fa-chevron-right"></i>
                          </a>
                        </div>
                      </div>
                    </div>
                    <div className="d-flex justify-content-between my-5">
                      <a
                        href="signup-interests-after-selected.html"
                        className="text-white text-nowrap align-self-center"
                      >
                        <img src={arrowleft} className="me-2" alt="" /> Back
                      </a>
                      {backgroundTick === true && profileTick === true ? (
                        <button
                          className="btn btn-primary text-nowrap btn-sm"
                          onClick={handleSubmit}
                        >
                          {" "}
                          Next
                          <img src={arrowright} className="ms-2" alt="" />
                        </button>
                      ) : (
                        <button
                          className="btn btn-outline-primary text-nowrap btn-sm"
                          onClick={handleSubmit}
                        >
                          {" "}
                          Skip
                          <img src={arrowright} className="ms-2" alt="" />
                        </button>
                      )}
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

const mapStateToProps = (state) => ({});

const mapDispatchToProps = {
  updateUserProfile,
};

export default connect(mapStateToProps, mapDispatchToProps)(UploadShotsScreen);
