import Constants from "./../../utils/Constants";

const { ACTIONS } = Constants;

let initialState = {
  notificationList: {},
  isNotificationPageLoading: false
};

export const notificationState = (state = initialState, action) => {
  const { type } = action;
  switch (type) {
    case ACTIONS.GET_NOTIFICATION_LIST:
      return { ...state, notificationList: action.payload };
    case ACTIONS.SHOW_NOTIFICATION_SCREEN_LOADING:
      return { ...state, isNotificationPageLoading: true };
    case ACTIONS.HIDE_NOTIFICATION_SCREEN_LOADING:
      return { ...state, isNotificationPageLoading: false };
    default:
      return state;
  }
};
