import Constants from "./../../utils/Constants";

const { ACTIONS } = Constants;

let initialState = {
  isCollabListLoading: false,
  requestedCollaborationsList: {},
  inprogressCollaborationsList: {},
  completedCollaborationsList: {},
  isCollabDetailLoading: false,
  collabDetailObj: {},
};

export const collabortionState = (state = initialState, action) => {
  const { type, requestedCollaborationsList, inprogressCollaborationsList, completedCollaborationsList, collabDetailObj } = action;

  switch (type) {
    case ACTIONS.SHOW_COLLABORATION_LIST_LOADING:
      return { ...state, isCollabListLoading: true };
    case ACTIONS.HIDE_COLLABORATION_LIST_LOADING:
      return { ...state, isCollabListLoading: false };
    case ACTIONS.GET_REQUESTED_COLLABORATION_LIST:
      return { ...state, requestedCollaborationsList };
    case ACTIONS.GET_INPROGRESS_COLLABORATION_LIST:
      return { ...state, inprogressCollaborationsList };
    case ACTIONS.GET_COMPLETED_COLLABORATION_LIST:
      return { ...state, completedCollaborationsList };
    case ACTIONS.SHOW_COLLABORATION_DETAILS_LOADING:
      return { ...state, isCollabDetailLoading: true };
    case ACTIONS.HIDE_COLLABORATION_DETAILS_LOADING:
      return { ...state, isCollabDetailLoading: false };
    case ACTIONS.GET_COLLABORATION_DETAILS:
      return { ...state, collabDetailObj };
    default:
      return state;
  }
};
