import React, { useEffect, useState } from "react";
import { connect } from "react-redux";

const arrStepper = [
  { isSelected: false },
  { isSelected: false },
  { isSelected: false },
  { isSelected: false },
  { isSelected: false },
  { isSelected: false },
  { isSelected: false },
];
const Stepper = (props) => {
  const [stepArray, setStepArray] = useState(arrStepper);

  useEffect(() => {
    arrStepper.forEach((element, index) => {
      if (index <= props.stepperActive) {
        element.isSelected = true;
      } else {
        element.isSelected = false;
      }

    });
    setStepArray(arrStepper)
  }, [props.stepperActive]);

  return (
    <div className="stepper">
      {stepArray.map((item, index) => {
        let colorWidth = (index + 1) * 50 + "px";
        if (index === props.stepperActive)
          return <div key={index} style={{ width: colorWidth }} className="steps active"></div>;
        else if (index >= props.stepperActive)
          return (<div key={index} className="steps"></div>);
        else
          return ("");
      })}
    </div>
  );
};

const mapStateToProps = (state) => ({});

const mapDispatchToProps = {};

export default connect(mapStateToProps, mapDispatchToProps)(Stepper);
