import Constants from "./../../utils/Constants";

const { ACTIONS } = Constants;

let initialState = {
  brandMenu: [],
  brandDashboardDetails: {},
  socialLogin: "",
  chatUnreadCount: {},
  isBrandDashboardLoading: false,
};

export const homeState = (state = initialState, action) => {
  const {
    type,
    brandMenu,
    brandDashboardDetails,
    socialLogin,
    chatUnreadCount,
  } = action;
  switch (type) {
    case ACTIONS.GET_MENU_LIST_SUCCESS:
      return { ...state, brandMenu };
    case ACTIONS.GET_BRAND_DASHBOARD_SUCCESS:
      return {
        ...state,
        brandDashboardDetails,
        socialLogin,
        isBrandDashboardLoading: false,
      };
    case ACTIONS.GET_MESSAGE_UNREAD_COUNT_SUCCESS:
      return { ...state, chatUnreadCount };
    case ACTIONS.SHOW_BRAND_DASHBOARD_LOADING:
      return { ...state, isBrandDashboardLoading: true };
    case ACTIONS.HIDE_BRAND_DASHBOARD_LOADING:
      return { ...state, isBrandDashboardLoading: false };
    case ACTIONS.LOGOUT_CLEAR_ALL_STATES:
      return { ...initialState };
    default:
      return state;
  }
};
