import Constants from "./../../../utils/Constants";
import HttpBaseClient from "./../../../network/HttpBaseClient";
import { GET_MENU_LIST, GET_DASHBOARD_DETAILS } from "./../../../utils/URL";
import { handleError } from "../NetworkAction";
import Toaster from "../../../utils/Toaster/Toast";

export const getMenuList = (role_name) => {
  return (dispatch) => {
    HttpBaseClient.post(GET_MENU_LIST, { role_name }, 1)
      .then((response) => {
        if (response.code === 0) {
          dispatch({
            type: Constants.ACTIONS.GET_MENU_LIST_SUCCESS,
            brandMenu: response.data,
          });
        } else {
          Toaster.error(response.message);
        }
      })
      .catch((error) => {
        dispatch(handleError(error));
      });
  };
};

export const getDashboardDetails = (user_role) => {
  return (dispatch) => {
    dispatch(showDashboardLoading());
    HttpBaseClient.post(GET_DASHBOARD_DETAILS, { user_role }, 1)
      .then((response) => {
        if (response.code === 0) {
          dispatch({
            type: Constants.ACTIONS.GET_BRAND_DASHBOARD_SUCCESS,
            brandDashboardDetails: response.data.dashboard,
            socialLogin: response.data.social_login,
          });
          dispatch({
            type: Constants.ACTIONS.GET_MESSAGE_UNREAD_COUNT_SUCCESS,
            chatUnreadCount: response.data.unread_count,
          });
          dispatch(hideDashboardLoading());
        } else {
          Toaster.error(response.message);
        }
      })
      .catch((error) => {
        dispatch(hideDashboardLoading());
        dispatch(handleError(error));
      });
  };
};

export const showDashboardLoading = () => {
  return (dispatch) => {
    dispatch({
      type: Constants.ACTIONS.SHOW_BRAND_DASHBOARD_LOADING,
    });
  };
};

export const hideDashboardLoading = () => {
  return (dispatch) => {
    dispatch({
      type: Constants.ACTIONS.HIDE_BRAND_DASHBOARD_LOADING,
    });
  };
};
