import React, { useEffect, useState } from "react";
import { connect, useSelector } from "react-redux";
import moment from "moment";
//Common Component
import SideMenu from "./../../components/SideMenu";
import PageLoader from "../../components/PageLoader";
//API Call
import { getNotificationlist } from "./../../redux/actions/NotificationAction";
import { getProfileImageOriginalUrl } from "../../utils/Utility";
//Image
import profileImg from "./../../assets/images/profile-vector.svg";
import ReactPaginate from "react-paginate";

export const NotificationsScreen = (props) => {
  const [perPage] = useState(10);

  const deviceState = useSelector((state) => state.deviceState);
  const notificationState = useSelector((state) => state.notificationState);
  const notificationList = notificationState.notificationList;

  useEffect(() => {
    let notificationPostData = {
      offset: 0,
      limit: 10,
    };
    getNotificationList(notificationPostData);
  }, []);

  const getNotificationList = (notificationPostData) => {
    props.getNotificationlist(notificationPostData);
  };

  const handlePageClick = (e) => {
    console.log(e.selected, "=======selected offset");
    const offset = e.selected * perPage;
    console.log(offset, "=======selected offset");
    const limit = perPage;
    let notificationPostData = {
      offset: offset,
      limit: limit,
    };
    getNotificationList(notificationPostData);
  };
  return (
    <div class="w-100 h-100 position-fixed">
      <div class="d-flex h-100">
        <SideMenu />
        <div class="w-100">
          <div class="container-fluid h-100">
            <div class="row h-100 mobile-overflow">
              <div class="col-md-10 pt-5 pb-3 h-desktop-100 desktop-overflow">
                <div class="row h-100">
                  <div class="col-lg-10 offset-lg-1">
                    <div class="d-flex">
                      <div>
                        <h1 class="fw-normal mb-0 display-3">notifications</h1>
                      </div>
                    </div>
                    {notificationState.isNotificationPageLoading ? (
                      <PageLoader
                        isLoading={notificationState.isNotificationPageLoading}
                        isLoggedin={true}
                      />
                    ) : (
                        <div class="mt-3 overflow-auto">
                          {notificationList.length
                            ? notificationList.map((value, index) => {
                              var arrnotification_description =
                                value.notification_description.split("with");
                              return (
                                <div class="d-flex border-bottom" key={index}>
                                  <div class="d-flex py-4 flex-fill bd-highlight">
                                    <div class="notifications-profile align-self-center me-4">
                                      <img
                                        src={
                                          value.notifiers.photo
                                            ? getProfileImageOriginalUrl(
                                              value.notifiers.photo,
                                              deviceState
                                            )
                                            : profileImg
                                        }
                                        class="img-fluid"
                                        alt=""
                                      />
                                    </div>
                                    <div class="align-self-center flex-grow-1">
                                      <p class="mb-0">
                                        {arrnotification_description[0] +
                                          " " +
                                          "with" +
                                          " "}
                                        {value.notification_created_by
                                          .brand_name
                                          ? value.notification_created_by
                                            .brand_name
                                          : value.notification_created_by
                                            .first_name +
                                          " " +
                                          value.notification_created_by
                                            .last_name}
                                        {arrnotification_description[1]}
                                      </p>
                                    </div>
                                  </div>
                                  <div class="list-inline align-self-center">
                                    <p class="text-secondary mb-0">
                                      {moment(value.created_at).format("ll")}
                                    </p>
                                  </div>
                                </div>
                              );
                            })
                            : null}
                        </div>
                      )}

                    <div>
                      <ReactPaginate
                        previousLabel={"prev"}
                        nextLabel={"next"}
                        breakLabel={"..."}
                        breakClassName={"break-me"}
                        pageCount={5}
                        marginPagesDisplayed={3}
                        pageRangeDisplayed={10}
                        onPageChange={handlePageClick}
                        containerClassName={"pagination"}
                        subContainerClassName={"pages pagination"}
                        activeClassName={"active"}
                      />
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

const mapStateToProps = (state) => ({});

const mapDispatchToProps = {
  getNotificationlist,
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(NotificationsScreen);
