import { combineReducers } from "redux";
import { persistReducer } from "redux-persist";
import storage from "@react-native-community/async-storage";

import { loginState } from "./LoginReducer";
import { signupState } from "./SignupReducer";
import { userRoleState } from "./UserRoleReducer";
import { deviceState } from "./UploadReducer";
import { registerState } from "./RegisterReducer";
import { notificationState } from "./NotificationReducer";
import { performanceState } from "./PerformanceReducer";
import { collabortionState } from "./CollaborationReducer";
import { profileState } from "./ProfileReducer";
import { discoverState } from "./DiscoverReducer";
import { homeState } from "./HomeReducer";
import { calendarState } from "./CalendarReducer";

const loginPersistConfig = {
  key: "loginData",
  storage: storage,
  whitelist: ["loginUserData"],
};
const userRolePersistConfig = {
  key: "userType",
  storage: storage,
  blacklist: ["isUserTypeLoading"],
};

const homeStatePersistConfig = {
  key: "brandMenu",
  storage: storage,
  whitelist: ["brandMenu"],
};

const deviceStatePersistConfig = {
  key: "s3ImageBucketName",
  key: "s3videoBucketName",
  key: "profileImageLocation",
  key: "s3postImageLocation",
  key: "s3storyImageLocation",
  key: "s3postVideoLocation",
  key: "s3campaignImageLocation",
  key: "s3storyVideoLocation",
  key: "s3thumbnailLocation",
  key: "s3documentLoaction",
  storage: storage,
  whitelist: [
    "s3ImageBucketName",
    "s3videoBucketName",
    "profileImageLocation",
    "s3postImageLocation",
    "s3storyImageLocation",
    "s3postVideoLocation",
    "s3campaignImageLocation",
    "s3storyVideoLocation",
    "s3thumbnailLocation",
    "s3documentLoaction",
  ],
};

//Combines all the reducer for the store and exports to it
const rootReducer = combineReducers({
  loginState: persistReducer(loginPersistConfig, loginState),
  signupState,
  userRoleState: persistReducer(userRolePersistConfig, userRoleState),
  deviceState: persistReducer(deviceStatePersistConfig, deviceState),
  registerState,
  notificationState,
  performanceState,
  collabortionState,
  profileState,
  discoverState,
  homeState: persistReducer(homeStatePersistConfig, homeState),
  calendarState,
});

export default rootReducer;
