import Constants from "./../../utils/Constants";

const { ACTIONS } = Constants;

let initialState = {
  performanceList: {},
  isPerformancePageLoading: false
};

export const performanceState = (state = initialState, action) => {
  const { type } = action;
  switch (type) {
    case ACTIONS.GET_PERFORMANCE_LIST:
      return { ...state, performanceList: action.payload };
    case ACTIONS.SHOW_PERFORMANCE_SCREEN_LOADING:
      return { ...state, isPerformancePageLoading: true };
    case ACTIONS.HIDE_PERFORMANCE_SCREEN_LOADING:
      return { ...state, isPerformancePageLoading: false };
    default:
      return state;
  }
};
