import React from "react";
import { connect } from "react-redux";
import { Link, useHistory } from "react-router-dom";
import store from "store";
//Common Component
import Stepper from "../../components/Stepper";
import PopupWindow from "../../components/PopupWindow";
//Image
import logo from "./../../assets/images/logo.svg";
import arrowleft from "./../../assets/images/arrow-left.svg";
//API Call
import { linkInstaAuthFan } from "../../redux/actions/SignupAction";

export const ConnectSocialLoginScreen = (props) => {
  const userDetail = store.get("user");
  const history = useHistory();

  const navigateToInstagram = async () => {
    return new Promise(async (resolve, reject) => {
      let uri = userDetail.user.web_url;
      let instagramWindow = await PopupWindow(uri, "", window, 1000, 600);
      var timer = setInterval(async function () {
        if (instagramWindow.closed) {
          clearInterval(timer);
          try {
            var url = new URL(instagramWindow.document.documentURI);
            let token = url.searchParams.get("code");
            if (!token) {
              reject(null);
            }
            resolve(token);
          } catch (ex) {
            reject(null);
          }
        }
      }, 1000);
    });
  };

  const myInstagramLogin = async () => {
    try {
      let token = await navigateToInstagram();
      let params = {
        access_token: token,
        redirect_url: userDetail.user.web_redirect_url,
      };
      props.linkInstaAuthFan(params, () => {
        setTimeout(() => {
          history.push("/social-login-success");
        }, 1000);
      });
    } catch (ex) {
      console.log(ex);
    }
  };

  return (
    <div className="w-100 h-100 position-fixed">
      <div className="w-100 h-100 d-flex overflow-auto">
        <div className="container-fluid">
          <div className="row h-100">
            <div className="col-sm-12">
              <div className="d-flex flex-column h-100">
                <div className="logo">
                  <img src={logo} className="mt-3" alt="" />
                </div>
                <div className="row h-100">
                  <div className="col-sm-6 pt-4 offset-sm-3 d-flex flex-column all-contents">
                    <p className="text-muted small fw-bold mb-1">6/7</p>
                    <Stepper stepperActive={5} />
                    <h1 className="mt-5 pb-5 display-3">Last but not least!</h1>
                    <div className="">
                      <p className="text-secondary">
                        To get started in Embassy, we need to connect your
                        Instagram!
                      </p>
                      <p className="text-secondary">
                        This is so we can verify your brand and so you can start
                        collaborating with creators on social!
                      </p>
                    </div>
                    <div
                      className="d-flex signup-photos"
                      onClick={() => myInstagramLogin()}
                    >
                      <div className="d-flex py-3 flex-fill bd-highlight">
                        <div className="align-self-center me-4">
                          <i className="fab fa-instagram fa-lg"></i>
                        </div>
                        <div className="profile-msg align-self-center flex-grow-1">
                          <h5 className="mb-0 text-white display-6">
                            Sign in with instagram
                          </h5>
                        </div>
                      </div>
                      <div className="list-inline align-self-center text-center">
                        <i className="far fa-chevron-right"></i>
                      </div>
                    </div>
                    <div className="d-flex justify-content-between mb-5 mt-auto">
                      <Link
                        to="/add-bio"
                        className="text-white text-nowrap align-self-center"
                      >
                        <img src={arrowleft} alt="" className="me-2" />
                        Back
                      </Link>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
const mapStateToProps = (state) => ({});

const mapDispatchToProps = {
  linkInstaAuthFan,
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(ConnectSocialLoginScreen);
