import React from "react";
import { connect, useSelector } from "react-redux";
import { useHistory } from "react-router-dom";
// Image
import profileFive from "../assets/images/sample/kendric.svg";
// API Call
import { getProfileImageOriginalUrl } from "./../utils/Utility"

export const BrandOrderListItem = (props) => {
  const { collaborationItem } = props;
  const history = useHistory();

  const navigateCollabDetails = (_id) => {
    history.push("/brand-collab-detail/" + _id)
  }
  // From Reducer
  const deviceState = useSelector((state) => state.deviceState);

  return (
    <div
      className="rounded-1 border border-2 text-center d-xl-flex mb-3"
      onClick={() => navigateCollabDetails(collaborationItem._id)}
    >
      <div className="p-4">
        <img className="collab-profile-img" src={collaborationItem && collaborationItem.ordered_by && collaborationItem.ordered_by.photo ?
          getProfileImageOriginalUrl(collaborationItem.ordered_by.photo, deviceState) :
          profileFive} alt=""
        />
      </div>
      <div className="py-4">
        <p className="mb-0 text-xl-start">
          {collaborationItem && collaborationItem.influencer ?
            collaborationItem.influencer.first_name && collaborationItem.influencer.last_name ?
              collaborationItem.influencer.first_name + " " + collaborationItem.influencer.last_name :
              collaborationItem.influencer.first_name ?
                collaborationItem.influencer.first_name :
                "" :
            ""
          }
        </p>
        <div className="d-xl-flex small text-secondary">
          <i className="align-self-center fab fa-instagram me-1"></i>
          <small className="align-self-center">{collaborationItem && collaborationItem.service_type ? collaborationItem.service_type : ""}</small>
        </div>
      </div>
      <div className="ms-auto pe-xl-3 pb-xl-0 pb-3">
        <small className={`xs-small ${collaborationItem && collaborationItem.status === "REQUESTED" ? `text-info` :
          collaborationItem && collaborationItem.status === "INPROGRESS" ? `text-warning` :
            collaborationItem && collaborationItem.status === "COMPLETED" ? `text-success` : ``} spacer`}>
          {collaborationItem && collaborationItem.status ? collaborationItem.status : ""}
        </small>
        <h5 className="mb-0">${collaborationItem && collaborationItem.overall_total_amount ? collaborationItem.overall_total_amount : ""}</h5>
      </div>
    </div>
  );
};

const mapStateToProps = (state) => ({});

const mapDispatchToProps = {};

export default connect(mapStateToProps, mapDispatchToProps)(BrandOrderListItem);
