import React, { useState } from "react";
import { connect } from "react-redux";
import { Link, useHistory } from "react-router-dom";
import { Formik } from "formik";
import * as Yup from "yup";
import { REGEX } from "./../../utils/Constants";
//Image
import envelope from "./../../assets/images/envelope.svg";
import eye from "./../../assets/images/eye.svg";
import eyeopen from "./../../assets/images/eye-visible.svg";
import logo from "./../../assets/images/logo.svg";
import mobile from "./../../assets/images/mobile.svg";
//API Call
import { initiateLogin } from "./../../redux/actions/LoginAction";
import { updateSelectedUser } from "./../../redux/actions/UserRoleAction";
import { useSelector } from "react-redux";
//Common Component
import ButtonLoader from "../../components/ButtonLoader";

export const LoginScreen = (props) => {
  const params = {
    email: "",
    password: "",
  };
  const [showPassword, setShowPassword] = useState(false);
  const history = useHistory();

  // From Reducer
  const loginState = useSelector((state) => state.loginState);

  // On Click API call
  const handleSubmit = async (values) => {
    props.initiateLogin({ ...values, device: {}, is_web: true }, history, (user) => {
      if (user) {
        if (user.user_role.length > 0) {
          if (Object.keys(user.user_role[0]).length > 0) {
            props.updateSelectedUser(user.user_role[0], true);
            if (user.profile_status === "INACTIVE") {
              history.push("/about-you");
            } else if (
              !user.is_skipimage &&
              (!user.isBackgroundImageUploaded || !user.isProfileImageUploaded)
            ) {
              history.push("/upload-shots");
            } else if (user.social_login === "INACTIVE") {
              history.push("/connect-social-login");
            } else {
              if (user.user_role[0].role_name !== "INFLUENCER") {
                history.push("/brand-home");
              } else if (user.user_role[0].role_name === "INFLUENCER") {
                history.push("/connect-social-login");
              } else {
                history.push("/brand-home");
              }
            }
          } else {
            history.push("/select-user-role");
          }
        } else {
          history.push("/select-user-role");
        }
      }
    });
  };

  // Validation
  const formValidationSchema = Yup.object().shape({
    email: Yup.string()
      .email("Enter valid email address")
      .matches(REGEX.EMAIL, "Enter valid email address")
      .required("Email is required"),

    password: Yup.string("Invalid password")
      // .matches(REGEX.PASSWORD, "Enter valid password")
      .required("Password is required"),
  });

  return (
    <div className="w-100 h-100 position-fixed">
      <div className="w-100 h-100 d-flex overflow-auto">
        <div className="container-fluid">
          <div className="row h-100">
            <div className="col-sm-6">
              <div className="d-flex flex-column h-100">
                <div className="logo">
                  <img alt="" src={logo} className="mt-3" />
                </div>
                <div className="row my-auto">
                  <div className="col-sm-6 offset-sm-3">
                    <h1 className="mb-5 display-3">Login</h1>
                    <Formik
                      initialValues={{ ...params }}
                      validationSchema={formValidationSchema}
                      onSubmit={handleSubmit}
                      enableReinitialize={true}
                    >
                      {({
                        values,
                        touched,
                        errors,
                        handleSubmit,
                        handleChange,
                      }) => {
                        return (
                          <form onSubmit={handleSubmit}>
                            <div className="mb-3">
                              <label className="form-label">Email</label>
                              <div className="input-group flex-nowrap">
                                <input
                                  name="email"
                                  id="email"
                                  type="text"
                                  className="form-control"
                                  placeholder="Enter email address..."
                                  onChange={handleChange}
                                  value={values.email}
                                />
                                <span className="input-group-text">
                                  <img src={envelope} alt="" />
                                </span>
                              </div>
                              <div>
                                {errors.email && touched.email ? (
                                  <div className="error-message">
                                    {errors.email}
                                  </div>
                                ) : null}
                              </div>
                            </div>
                            <div className="mb-3">
                              <label className="form-label">Password</label>
                              <div className="input-group flex-nowrap">
                                <input
                                  name="password"
                                  id="password"
                                  type={showPassword ? "text" : "password"}
                                  className="form-control"
                                  placeholder="Enter password..."
                                  onChange={handleChange}
                                  value={values.password}
                                />
                                <span
                                  className="input-group-text"
                                  onClick={() => {
                                    setShowPassword(!showPassword);
                                  }}
                                >
                                  <img
                                    src={showPassword ? eyeopen : eye}
                                    alt=""
                                  />
                                </span>
                              </div>
                              <div>
                                {errors.password && touched.password ? (
                                  <div className="error-message">
                                    {errors.password}
                                  </div>
                                ) : null}
                              </div>
                            </div>
                            <div className="form-check my-4">
                              <input
                                className="form-check-input me-3"
                                type="checkbox"
                                value=""
                              />
                              <label className="form-check-label small text-secondary">
                                Keep me logged in
                              </label>
                            </div>
                            <button
                              className={
                                loginState.isLoginLoading
                                  ? "btn btn-primary w-100 disabled"
                                  : "btn btn-primary w-100"
                              }
                              type="submit"
                            >
                              {loginState.isLoginLoading ? (
                                <ButtonLoader />
                              ) : (
                                  "Login"
                                )}
                            </button>
                            <div className="text-center">
                              <p className="mb-2 mt-3">
                                Don't have an account?{" "}
                                <Link to="/sign-up">Sign Up</Link>
                              </p>
                              <Link to="/sign-up">Forgot Password?</Link>
                            </div>
                          </form>
                        );
                      }}
                    </Formik>
                  </div>
                </div>
                <div class="footer d-flex w-100 justify-content-end">
                  <p>build version 1.0.0</p>
                </div>
              </div>
            </div>
            <div className="col-sm-6 login-bg">
              <div className="row h-100">
                <div className="col-sm-10 offset-sm-1 align-self-center">
                  <img className="img-fluid" src={mobile} alt="" />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

const mapStateToProps = (state) => ({});

const mapDispatchToProps = {
  initiateLogin,
  updateSelectedUser,
};

export default connect(mapStateToProps, mapDispatchToProps)(LoginScreen);
