import React, { useEffect } from "react";
import { connect, useSelector } from "react-redux";
import { useHistory } from "react-router-dom";
import {
  getUserRoles,
  updateSelectedUser,
} from "../../redux/actions/UserRoleAction";
// Image
import logo from "./../../assets/images/logo.svg";
import arrowRight from "./../../assets/images/arrow-right.svg";
import creator from "./../../assets/images/creator.svg";
import brand from "./../../assets/images/brand.svg";
// Common Component
import Stepper from "../../components/Stepper";
import PageLoader from "../../components/PageLoader";

export const SelectUserRoleScreen = (props) => {
  const userRoleState = useSelector((state) => state.userRoleState);
  const history = useHistory();

  useEffect(() => {
    props.getUserRoles();
  }, []);

  const onUserSelect = (pos) => {
    props.updateSelectedUser(userRoleState.userRoles[pos], false, history);
  };

  const capitalize = (s) => {
    if (typeof s !== "string") {
      return "";
    }
    return s.charAt(0).toLowerCase() + s.slice(1).toLowerCase();
  };

  const renderUserSelectRole = () => {
    if (userRoleState.isUserTypeLoading) {
      return <PageLoader isLoading={userRoleState.isUserTypeLoading} isLoggedin={false} />
    } else {
      return (
        <div className="container-fluid">
          <div className="row h-100">
            <div className="col-sm-12">
              <div className="d-flex flex-column h-100">
                <div className="logo">
                  <img src={logo} className="mt-3" alt="" />
                </div>
                <div className="row h-100">
                  <div className="col-sm-6 pt-4 offset-sm-3 d-flex flex-column all-contents">
                    <p className="text-muted small fw-bold mb-1">1/7</p>
                    <Stepper stepperActive={0} />
                    <h1 className="mb-5 mt-4 display-3">Hey! Which are you?</h1>
                    <div className="row mt-5">
                      <div className="col-lg-6 mb-4">
                        <div className="card bg-dark p-4 h-100">
                          <div className="d-flex mb-5">
                            <img src={creator} alt="" />
                          </div>
                          <h2>
                            {userRoleState.userRoles.length
                              ? capitalize(userRoleState.userRoles[0].label)
                              : ""}
                          </h2>
                          <p className="text-light">
                            {userRoleState.userRoles.length
                              ? capitalize(
                                userRoleState.userRoles[0].description[0]
                                  .value
                              )
                              : ""}
                          </p>
                          <div className="d-flex justify-content-end align-items-center pt-5">
                            <p className="me-3 mb-0">This is me!</p>
                            <a
                              href="signup-personal-info.html"
                              className="btn btn-primary rounded-circle"
                            >
                              <img src={arrowRight} alt="" />
                            </a>
                          </div>
                        </div>
                      </div>
                      <div className="col-lg-6 mb-4">
                        <div className="card bg-dark p-4 h-100">
                          <div className="d-flex mb-5">
                            <img src={brand} alt="" />
                          </div>
                          <h2>
                            {userRoleState.userRoles.length
                              ? capitalize(userRoleState.userRoles[1].label)
                              : ""}
                          </h2>
                          <p className="text-light">
                            {userRoleState.userRoles.length
                              ? capitalize(
                                userRoleState.userRoles[1].description[0]
                                  .value
                              )
                              : ""}
                          </p>
                          <div className="d-flex justify-content-end align-items-center pt-5">
                            <p className="me-3 mb-0">This is me!</p>
                            <button
                              className="btn btn-primary rounded-circle"
                              onClick={() => onUserSelect(1)}
                            >
                              <img src={arrowRight} alt="" />
                            </button>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      )
    }
  }


  return (
    <div className="w-100 h-100 position-fixed">
      <div className="w-100 h-100 d-flex overflow-auto">
        {renderUserSelectRole()}
      </div>
    </div>
  );
};

const mapStateToProps = (state) => ({});

const mapDispatchToProps = { getUserRoles, updateSelectedUser };

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(SelectUserRoleScreen);
