import Constants from "../../utils/Constants";

const { ACTIONS } = Constants;

let initialState = {
  s3ImageBucketName: "",
  s3videoBucketName: "",
  profileImageLocation: "",
  s3postImageLocation: "",
  s3storyImageLocation: "",
  s3postVideoLocation: "",
  s3campaignImageLocation: "",
  s3storyVideoLocation: "",
  s3thumbnailLocation: "",
  s3documentLoaction: "",
  isModalVisible: false,
  totalCount: 0,
  activeCount: 0,
  uploadStatus: -1,
  progress: 0,
  isUploaded: false,
};

export const deviceState = (state = initialState, action) => {
  const {
    type,
    s3ImageBucketName,
    s3videoBucketName,
    profileImageLocation,
    s3postImageLocation,
    s3documentLoaction,
    s3storyImageLocation,
    s3postVideoLocation,
    s3campaignImageLocation,
    s3storyVideoLocation,
    s3thumbnailLocation,
    totalCount, activeCount, progress
  } = action;
  switch (type) {
    case ACTIONS.UPDATE_S3_BUCKET_DETAILS:
      return {
        ...state,
        s3ImageBucketName,
        s3videoBucketName,
        profileImageLocation,
        s3postImageLocation,
        s3storyImageLocation,
        s3postVideoLocation,
        s3campaignImageLocation,
        s3storyVideoLocation,
        s3thumbnailLocation,
        s3documentLoaction,
      };

    case ACTIONS.UPLOADING_INIT:
      return { ...state, isModalVisible: true, uploadStatus: 0 };
    case ACTIONS.UPLOADING_STARTED:
      return {...state, uploadStatus: 1, totalCount, activeCount, progress: 0};
    case ACTIONS.UPLOADING_FINALIZING:
      return { ...state, uploadStatus: 2, isUploaded: false };
    case ACTIONS.UPLOADING_COMPLETED:
      return { ...state, uploadStatus: 3 };
    case ACTIONS.UPDATE_TOTAL_COUNT:
      return { ...state, totalCount };
    case ACTIONS.UPDATE_CURRENT_COUNT:
      return { ...state, activeCount };
    case ACTIONS.UPDATE_UPLOAD_PROGRESS:
      return { ...state, progress, isUploaded: true };
    default:
      return state;
  }
};