import Constants from "./../../../utils/Constants";
import HttpBaseClient from "./../../../network/HttpBaseClient";
import { GET_BRAND_COLLABORATION_LIST, GET_COLLABORATION_DETAILS } from "./../../../utils/URL";
import { handleError } from "../NetworkAction";
import Toaster from "./../../../utils/Toaster/Toast";

export const getCollaborationList = (params) => {
  return (dispatch) => {
    dispatch(showOrderListLoading());
    HttpBaseClient.post(GET_BRAND_COLLABORATION_LIST, params, 1)
      .then((response) => {
        dispatch(hideOrderListLoading());
        if (response.code === 0) {
          if (params.status === 'REQUESTED') {
            dispatch({
              type: Constants.ACTIONS.GET_REQUESTED_COLLABORATION_LIST,
              requestedCollaborationsList: response.data,
            });
          }
          if (params.status === "INPROGRESS") {
            dispatch({
              type: Constants.ACTIONS.GET_INPROGRESS_COLLABORATION_LIST,
              inprogressCollaborationsList: response.data,
            });
          }
          if (params.status === "COMPLETED") {
            dispatch({
              type: Constants.ACTIONS.GET_COMPLETED_COLLABORATION_LIST,
              completedCollaborationsList: response.data,
            });
          }
        } else if (response.code === 2) {
          Toaster.error(response.message);
        } else {
          Toaster.error(Constants.VALIDATION_MSG.REQ_FAILED);
        }
      })
      .catch((error) => {
        dispatch(hideOrderListLoading());
        // dispatch(handleError(error));
      });
  };
};

export const getCollaborationDetails = (order_id) => {
  return (dispatch) => {
    dispatch(showOrderDetailsLoading());
    HttpBaseClient.post(GET_COLLABORATION_DETAILS, { order_id }, 1)
      .then((response) => {
        dispatch(hideOrderDetailsLoading());
        if (response.code === 0) {
          dispatch({
            type: Constants.ACTIONS.GET_COLLABORATION_DETAILS,
            collabDetailObj: response.data,
          });
        } else {
          Toaster.error(Constants.VALIDATION_MSG.REQ_FAILED);
        }
      })
      .catch((error) => {
        dispatch(hideOrderDetailsLoading());
        // dispatch(handleError(error));
      });
  };
};

export const showOrderListLoading = () => {
  return (dispatch) => {
    dispatch({
      type: Constants.ACTIONS.SHOW_COLLABORATION_LIST_LOADING,
    });
  };
};

export const hideOrderListLoading = () => {
  return (dispatch) => {
    dispatch({
      type: Constants.ACTIONS.HIDE_COLLABORATION_LIST_LOADING,
    });
  };
};

export const showOrderDetailsLoading = () => {
  return (dispatch) => {
    dispatch({
      type: Constants.ACTIONS.SHOW_COLLABORATION_DETAILS_LOADING,
    });
  };
};

export const hideOrderDetailsLoading = () => {
  return (dispatch) => {
    dispatch({
      type: Constants.ACTIONS.HIDE_COLLABORATION_DETAILS_LOADING,
    });
  };
};