import Constants from "./../../utils/Constants";

const { ACTIONS } = Constants;

let initialState = {
  isUserTypeLoading: false, // Used to show loading when the user type api invoked.
  userRoles: [],
  selectedRole: undefined,
  userRolesDetails: undefined,
  subscriptionUserRoles: [],
  subscriptionUserRolesDetails: undefined,
};

export const userRoleState = (state = initialState, action) => {
  const { type, userRoles, selectedRole, userRolesDetails } = action;

  switch (type) {
    case ACTIONS.SHOW_USER_ROLE_LOADING:
      return { ...state, isUserTypeLoading: true };
    case ACTIONS.HIDE_USER_ROLE_LOADING:
      return { ...state, isUserTypeLoading: false };
    case ACTIONS.GET_NEW_USER_ROLES_SUCCESS:
      return { ...state, userRoles, userRolesDetails };
    case ACTIONS.UPDATE_SELECTED_USERS_ROLE:
      return { ...state, selectedRole };
    default:
      return state;
  }
};
