import Constants from "../../utils/Constants";
import HttpBaseClient from "../../network/HttpBaseClient";
import { GET_USER_ROLES } from "../../utils/URL";
import { handleError } from "./NetworkAction";

export const getUserRoles = () => {
  return (dispatch) => {
    dispatch(showUserRoleLoading());
    HttpBaseClient.get(GET_USER_ROLES, {}, 1)
      .then((response) => {
        if (response.code === 0) {
          dispatch({
            type: Constants.ACTIONS.GET_NEW_USER_ROLES_SUCCESS,
            userRoles: response.data.master_roles,
            userRolesDetails: response.data,
          });
        }
        dispatch(hideUserRoleLoading());
      })
      .catch((error) => {
        dispatch(hideUserRoleLoading());
        dispatch(handleError(error));
      });
  };
};

// Function to store the selected user role
export const updateSelectedUser = (selectedUser, isFromLogin, history) => {
  return (dispatch) => {
    dispatch({
      type: Constants.ACTIONS.UPDATE_SELECTED_USERS_ROLE,
      selectedRole: selectedUser,
    });
    if (!isFromLogin) {
      history.push("/about-you");
    }
  };
};

export const showUserRoleLoading = () => {
  return (dispatch) => {
    dispatch({
      type: Constants.ACTIONS.SHOW_USER_ROLE_LOADING,
    });
  };
};

export const hideUserRoleLoading = () => {
  return (dispatch) => {
    dispatch({
      type: Constants.ACTIONS.HIDE_USER_ROLE_LOADING,
    });
  };
};
