import React, { useState, useEffect } from "react";
import { connect, useSelector } from "react-redux";
import { Link, useHistory } from "react-router-dom";
import { Formik } from "formik";
import * as Yup from "yup";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import Select from "react-select";
import moment from "moment";
// Image
import logo from "./../../assets/images/logo.svg";
import arrowleft from "./../../assets/images/arrow-left.svg";
import arrowright from "./../../assets/images/arrow-right.svg";
import calendar from "./../../assets/images/calendar.svg";
// API Call
import { getGoogleLocations } from "../../redux/actions/common/CommonAction";
import { saveAboutYouDetails } from "../../redux/actions/SignupAction";
// Common Component
import Stepper from "../../components/Stepper";

export const AboutYouScreen = (props) => {
  const history = useHistory();

  const [params, setParams] = useState({
    brand_name: "",
    first_name: "",
    last_name: "",
    dob: "",
    gender: "",
    address: {},
  });

  const [locationLoading, setLocationLoading] = useState(false);
  const [locationArray, setLocationArray] = useState([]);

  // From reducer
  const signupState = useSelector((state) => state.signupState);

  useEffect(() => {
    if (Object.keys(signupState.aboutUserDetails).length !== 0) {
      setParams(signupState.aboutUserDetails);
    }
  }, [signupState.aboutUserDetails]);

  const handleInputChange = (newValue) => {
    const inputValue = newValue;
    if (inputValue.length) {
      setLocationLoading(true);
      props.getGoogleLocations({ search: newValue }, (result, response) => {
        setLocationLoading(false);
        if (result) {
          setLocationArray(response);
        }
      });
    }
    return inputValue;
  };

  const genderList = ["Male", "Female"];

  const locationStyles = {
    control: (styles) => ({
      ...styles,
      backgroundColor: "#10183e",
      borderColor: "#888c9f",
      lineHeight: 2,
    }),
    option: (styles) => ({ ...styles, color: "#10183e" }),
    singleValue: (styles) => ({ ...styles, color: "#fff" }),
  };

  //Validation
  const formValidationSchema = Yup.object().shape({
    brand_name: Yup.string()
      .required("Brand name is required")
      .max(20, "Maximum of 20 characters"),
    first_name: Yup.string()
      .required("First name is required")
      .max(12, "Maximum of 12 characters"),
    last_name: Yup.string()
      .required("Last name is required")
      .max(12, "Maximum of 12 characters"),
    dob: Yup.date().required("Birth day is required"),
    gender: Yup.string().required("Sex is required"),
    address: Yup.object().required("Location is required"),
  });

  // On Click API call
  const handleSubmit = (values) => {
    let userObject = {
      first_name: values.first_name,
      last_name: values.last_name,
      dob: moment(values.dob).format("MM/DD/YYYY"),
      gender: values.gender,
      description: "",
      address: values.address.label,
      city: "",
      state: "",
      country: "",
      brand_name: values.brand_name,
      isNcaaAthlete: false,
      school: {},
    };
    props.saveAboutYouDetails(userObject, () => {
      history.push("/select-interest");
    });
  };

  return (
    <div className="w-100 h-100 position-fixed">
      <div className="w-100 h-100 d-flex overflow-auto">
        <div className="container-fluid">
          <div className="row h-100">
            <div className="col-sm-12">
              <div className="d-flex flex-column h-100">
                <div className="logo">
                  <img src={logo} className="mt-3" alt="" />
                </div>
                <div className="row h-100">
                  <div className="col-sm-6 pt-4 offset-sm-3 d-flex flex-column all-contents">
                    <p className="text-muted small fw-bold mb-1">2/7</p>
                    <Stepper stepperActive={1} />
                    <h1 className="mb-5 mt-4 display-3">Tell us more?</h1>
                    <Formik
                      initialValues={{ ...params }}
                      validationSchema={formValidationSchema}
                      onSubmit={handleSubmit}
                      enableReinitialize={true}
                    >
                      {({
                        values,
                        touched,
                        errors,
                        handleSubmit,
                        handleChange,
                        setFieldValue,
                      }) => {
                        return (
                          <form onSubmit={handleSubmit}>
                            <div className="row">
                              <div className="col-12">
                                <div className="mb-3">
                                  <label className="form-label">
                                    Brand Name
                                  </label>
                                  <input
                                    name="brand_name"
                                    maxLength={20}
                                    type="text"
                                    className="form-control"
                                    id="brand_name"
                                    placeholder="The next big thing"
                                    value={values.brand_name}
                                    onChange={handleChange}
                                  />
                                  {errors.brand_name && touched.brand_name ? (
                                    <div className="error-message">
                                      {errors.brand_name}
                                    </div>
                                  ) : null}
                                </div>
                              </div>
                            </div>
                            <div className="row">
                              <div className="col-6">
                                <div className="mb-3">
                                  <label className="form-label">
                                    First Name
                                  </label>
                                  <input
                                    name="first_name"
                                    maxLength={12}
                                    type="text"
                                    className="form-control"
                                    id="first_name"
                                    placeholder="Greatest"
                                    value={values.first_name}
                                    onChange={handleChange}
                                  />
                                  {errors.first_name && touched.first_name ? (
                                    <div className="error-message">
                                      {errors.first_name}
                                    </div>
                                  ) : null}
                                </div>
                              </div>
                              <div className="col-6">
                                <div className="mb-3">
                                  <label className="form-label">
                                    Last Name
                                  </label>
                                  <input
                                    name="last_name"
                                    maxLength={12}
                                    type="text"
                                    className="form-control"
                                    id="last_name"
                                    placeholder="Ever"
                                    value={values.last_name}
                                    onChange={handleChange}
                                  />
                                  {errors.last_name && touched.last_name ? (
                                    <div className="error-message">
                                      {errors.last_name}
                                    </div>
                                  ) : null}
                                </div>
                              </div>
                            </div>
                            <div className="row">
                              <div className="col-6">
                                <div className="mb-3">
                                  <label className="form-label">dob</label>
                                  <div className="input-group flex-nowrap">
                                    <DatePicker
                                      name="dob"
                                      className="form-control about-calendar-border"
                                      placeholder="MM/DD/YYYY"
                                      selected={
                                        (values.dob && new Date(values.dob)) ||
                                        null
                                      }
                                      value={values.dob}
                                      onChange={(event) => {
                                        setFieldValue("dob", event);
                                      }}
                                      dropdownMode="select"
                                      showMonthDropdown
                                      showYearDropdown
                                      yearDropdownItemNumber={80}
                                      scrollableYearDropdown
                                      minDate={
                                        new Date(
                                          moment(
                                            new Date(),
                                            "DD-MM-YYYY"
                                          ).subtract(80, "years")
                                        )
                                      }
                                      maxDate={
                                        new Date(
                                          moment(
                                            new Date(),
                                            "DD-MM-YYYY"
                                          ).subtract(13, "years")
                                        )
                                      }
                                      autocomplete="off"
                                    />
                                    <span className="input-group-text">
                                      <img src={calendar} alt="" />
                                    </span>
                                  </div>
                                  {errors.dob && touched.dob ? (
                                    <div className="error-message">
                                      {errors.dob}
                                    </div>
                                  ) : null}
                                </div>
                              </div>
                              <div className="col-6">
                                <div className="mb-3">
                                  <label className="form-label">Sex</label>
                                  <select
                                    name="gender"
                                    className="form-control"
                                    id="gender"
                                    onChange={handleChange}
                                    value={values.gender}
                                  >
                                    <option value="">Please select</option>
                                    {genderList &&
                                      genderList.length &&
                                      genderList.map((value, index) => {
                                        return (
                                          <option value={value} key={index}>
                                            {value}
                                          </option>
                                        );
                                      })}
                                  </select>
                                  {errors.gender && touched.gender ? (
                                    <div className="error-message">
                                      {errors.gender}
                                    </div>
                                  ) : null}
                                </div>
                              </div>
                            </div>
                            <div className="row">
                              <div className="col-12">
                                <div className="mb-3">
                                  <label className="form-label">Location</label>
                                  <Select
                                    styles={locationStyles}
                                    classNamePrefix="search.."
                                    name="address"
                                    value={values.address}
                                    isLoading={locationLoading}
                                    options={locationArray}
                                    placeholder="Search locations.."
                                    onInputChange={handleInputChange}
                                    onChange={(event) => {
                                      setFieldValue("address", event);
                                    }}
                                    theme={(theme) => ({
                                      ...theme,
                                      borderRadius: 0,
                                      lineHeight: 2,
                                      colors: {
                                        ...theme.colors,
                                        primary: "lightgrey",
                                      },
                                    })}
                                  />
                                  {errors.address && touched.address ? (
                                    <div className="error-message">
                                      {errors.address}
                                    </div>
                                  ) : null}
                                </div>
                              </div>
                            </div>
                            <div className="d-flex justify-content-between my-5 pt-5">
                              <Link
                                to="/select-user-role"
                                className="text-white text-nowrap align-self-center"
                              >
                                <img src={arrowleft} alt="" className="me-2" />
                                Back
                              </Link>
                              <button
                                className="btn btn-primary text-nowrap btn-sm"
                                type="submit"
                              >
                                {" "}
                                Next
                                <img src={arrowright} className="ms-2" alt="" />
                              </button>
                            </div>
                          </form>
                        );
                      }}
                    </Formik>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
const mapStateToProps = (state) => ({});

const mapDispatchToProps = { getGoogleLocations, saveAboutYouDetails };

export default connect(mapStateToProps, mapDispatchToProps)(AboutYouScreen);
