import { BrowserRouter, Switch, Route } from "react-router-dom";
import LoginScreen from "./../screens/Login/LoginScreen";
import SignUpScreen from "./../screens/SignUp/SignUpScreen";
import VerifyScreen from "./../screens/SignUp/VerifyScreen";
import SelectUserRoleScreen from "./../screens/SignUp/SelectUserRoleScreen";
import AboutYouScreen from "./../screens/SignUp/AboutYouScreen";
import SelectInterestScreen from "./../screens/SignUp/SelectInterestScreen";
import UploadShotsScreen from "../screens/SignUp/UploadShotsScreen";
import ConnectSocialLoginScreen from "../screens/SignUp/ConnectSocialLoginScreen";
import AddBioScreen from "../screens/SignUp/AddBioScreen";
import BrandCollabListScreen from "../screens/Collaborations/BrandCollabListScreen";
import NotificationsScreen from "../screens/Notifications/NotificationsScreen";
import PerformanceScreen from "../screens/Performance/PerformanceScreen";
import BrandHomeScreen from "../screens/home/BrandHomeScreen";
import RedirectScreen from "../components/RedirectScreen";
import BrandSettingsScreen from "../screens/settings/BrandSettingsScreen";
import SocialLoginSuccess from "../screens/SignUp/SocialLoginSuccess";
import DiscoverScreen from "../screens/discover/DiscoverScreen";
import MessengerScreen from "../screens/messenger/MessengerScreen";
import BrandProfileScreen from "../screens/profile/BrandProfileScreen";
import BrandCollabDetailScreen from "../screens/Collaborations/BrandCollabDetailScreen";
import CalendarScreen from "../screens/calendar/CalendarScreen";

const Routes = () => {
  return (
    <BrowserRouter>
      <Switch>
        <Route exact path="/" component={LoginScreen} />
        <Route exact path="/sign-up" component={SignUpScreen} />
        <Route exact path="/verify" component={VerifyScreen} />
        <Route
          exact
          path="/select-user-role"
          component={SelectUserRoleScreen}
        />
        <Route exact path="/about-you" component={AboutYouScreen} />
        <Route exact path="/select-interest" component={SelectInterestScreen} />
        <Route exact path="/upload-shots" component={UploadShotsScreen} />
        <Route
          exact
          path="/connect-social-login"
          component={ConnectSocialLoginScreen}
        />
        <Route exact path="/add-bio" component={AddBioScreen} />
        <Route
          exact
          path="/brand-collaborations"
          component={BrandCollabListScreen}
        />
        <Route exact path="/notifications" component={NotificationsScreen} />
        <Route exact path="/performance" component={PerformanceScreen} />
        <Route exact path="/brand-home" component={BrandHomeScreen} />
        <Route exact path="/redirect" component={RedirectScreen} />
        <Route exact path="/brand-settings" component={BrandSettingsScreen} />
        <Route
          exact
          path="/social-login-success"
          component={SocialLoginSuccess}
        />
        <Route exact path="/discover" component={DiscoverScreen} />
        <Route exact path="/messenger" component={MessengerScreen} />
        <Route exact path="/brand-profile" component={BrandProfileScreen} />
        <Route
          exact
          path="/brand-collab-detail/:_id"
          component={BrandCollabDetailScreen}
        />
        <Route exact path="/calendar" component={CalendarScreen} />
      </Switch>
    </BrowserRouter>
  );
};

export default Routes;
