import HttpBaseClient from "../../../network/HttpBaseClient";
import { GET_GOOGLE_LOCATION } from "../../../utils/URL";
import { handleError } from "../NetworkAction";

export const getGoogleLocations = (searchValue, callback) => {
  console.log("---searchValue---->", searchValue);
  return (dispatch) => {
    try {
      console.log("---try---->");
      HttpBaseClient.post(GET_GOOGLE_LOCATION, searchValue, 1)
        .then(async (response) => {
          if (response.code === 0) {
            console.log("---response---->", response);
            const arrConversion = await arrayIntoLabelAndValueLocation(
              response.data.predictions
            );
            console.log("---arrConversion---->", arrConversion);
            callback(true, arrConversion);
          } else {
            callback(false);
          }
        })
        .catch((error) => {
          dispatch(handleError(error));
        });
    } catch (error) {
      console.log("---catch error---->");
      callback(false);
    }
  };
};

export const arrayIntoLabelAndValueLocation = async (array) => {
  if (!array.length) return [];
  const newArr = [];
  array.forEach((value) => {
    if (value.terms.length > 2) {
      newArr.push({ label: value.description, value: value });
    }
  });
  return newArr;
};
