import React from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";

export const ReceiverChat = (props) => {
  return (
    <div class="bubble-set d-flex pb-2 flex-row">
      <div class="msg-text">
        <div class="bubble-text bg-blue text-white lh-sm">
          Hey there! Love your clothing haul videos and we would love for you to
          do a series of those videos for our new fall line. We would like three
          videos: one for tops, one for outerwear, and one for footwear. Each of
          them a week apart. Thanks! Hope to work together!
        </div>
        <div class="text-details text-start mt-1">
          <span class="text-light-50">2:10 PM</span>
        </div>
      </div>
    </div>
  );
};

ReceiverChat.propTypes = {
  props: PropTypes,
};

const mapStateToProps = (state) => ({});

const mapDispatchToProps = {};

export default connect(mapStateToProps, mapDispatchToProps)(ReceiverChat);
