import React, { useEffect, useState } from "react";
import PropTypes from "prop-types";
import { connect, useSelector } from "react-redux";
import { Link, useHistory } from "react-router-dom";
// Image
import logo from "./../../assets/images/logo.svg";
import arrowleft from "./../../assets/images/arrow-left.svg";
import arrowright from "./../../assets/images/arrow-right.svg";
// API Call
import {
  registerUserDetails,
  getInterests,
  changeSelection,
} from "../../redux/actions/RegisterAction";
// Common Component
import Stepper from "../../components/Stepper";
import PageLoader from "../../components/PageLoader";

export const SelectInterestScreen = (props) => {
  const history = useHistory();
  const registerState = useSelector((state) => state.registerState);
  const userRoleState = useSelector((state) => state.userRoleState);
  const signupState = useSelector((state) => state.signupState);

  const [interestArr, setInterestArr] = useState([]);
  const [showNextButton, setShowNextButton] = useState(false);

  useEffect(() => {
    props.getInterests();
  }, []);

  useEffect(() => {
    setInterestArr(registerState.interestArray);
  }, [registerState.interestArray]);

  // On Click API call
  const onNextClick = (next) => {
    let userRoleId = userRoleState.selectedRole.roles_id;
    let userRole = userRoleState.selectedRole.role_name;
    let aboutUserDetails = signupState.aboutUserDetails;
    if (next) {
      props.registerUserDetails(
        aboutUserDetails,
        interestArr,
        userRoleId,
        userRole,
        history
      );
    } else {
      props.registerUserDetails(
        aboutUserDetails,
        [],
        userRoleId,
        userRole,
        history
      );
    }
  };

  const onClick = (index, selectionStatus, id) => {
    // filtering already selected interest and pushing it in separate array
    let selectedArray = registerState.interestArray.filter(
      (item) => item.is_selected === true
    );
    if (selectedArray.length < 8 || selectionStatus === true) {
      for (let i = 0; i < interestArr.length; i++) {
        if (interestArr[i]._id === id) {
          interestArr[i].is_selected = !selectionStatus;
        }
      }
      setInterestArr(interestArr);
      props.changeSelection(
        index,
        !selectionStatus,
        id,
        registerState.interestArray
      );
      let selectedArr = registerState.interestArray.filter(
        (item) => item.is_selected === true
      );
      if (selectedArr.length === 0) {
        setShowNextButton(false);
      } else {
        setShowNextButton(true);
      }
    } else {
      let selectedArr = registerState.interestArray.filter(
        (item) => item.is_selected === true
      );
      if (selectedArr.length === 0) {
        setShowNextButton(false);
      }
    }
  };

  const _renderInterestList = () => {
    const tempArray = [];
    if (registerState.isInterestLoading) {
      return <PageLoader isLoading={registerState.isInterestLoading} />;
    } else {
      if (interestArr.length > 0) {
        interestArr.map((data, index) => {
          tempArray.push(
            <div
              className="d-inline-block mb-2 me-1"
              key={index}
              onClick={() => {
                onClick(index, data.is_selected, data._id);
              }}
            >
              <span
                className={
                  data.is_selected
                    ? "badge badge-pill badge-info font-weight-normal"
                    : "badge badge-pill badge-primary font-weight-normal"
                }
              >
                {data.interest_name}
              </span>
            </div>
          );
        });
        return tempArray;
      } else {
        return (
          <div className="d-inline-block mb-2 me-1">
            <span className="font-weight-normal">No interests found</span>
          </div>
        );
      }
    }
  };

  const handleSearch = (event) => {
    let interestData = registerState.interestArray;
    let filteredInterest = interestData.filter((item) => {
      return item.interest_name
        .toLowerCase()
        .match(event.target.value.trim().toLowerCase());
    });
    setInterestArr(filteredInterest);
  };

  return (
    <div className="w-100 h-100 position-fixed">
      <div className="w-100 h-100 d-flex overflow-auto">
        <div className="container-fluid">
          <div className="row h-100">
            <div className="col-sm-12">
              <div className="d-flex flex-column h-100">
                <div className="logo">
                  <img src={logo} className="mt-3" alt="" />
                </div>
                <div className="row h-100">
                  <div className="col-sm-6 pt-4 offset-sm-3 d-flex flex-column all-contents">
                    <p className="text-muted small fw-bold mb-1">3/7</p>
                    <Stepper stepperActive={2} />
                    <h1 className="my-5 pb-5 display-3">
                      Which industries best <br />
                      fit your brand?
                    </h1>
                    <div className="row">
                      <div className="col-12">
                        <div className="mb-3">
                          <div className="input-group flex-nowrap">
                            <input
                              onChange={(event) => {
                                handleSearch(event);
                              }}
                              type="text"
                              className="form-control"
                              placeholder="Search and select up to 8 industries"
                            />
                            <span className="input-group-text">
                              <i className="fal fa-search"></i>
                            </span>
                          </div>
                        </div>
                      </div>
                    </div>
                    <div className="badge-scroll align-self-center text-center overflow-auto pt-3">
                      {_renderInterestList()}
                    </div>
                    <div className="d-flex justify-content-between my-5">
                      <Link
                        to="/about-you"
                        className="text-white text-nowrap align-self-center"
                      >
                        <img src={arrowleft} alt="" className="me-2" />
                        Back
                      </Link>
                      {!showNextButton ? (
                        <button
                          className="btn btn-outline-primary text-nowrap btn-sm"
                          onClick={() => {
                            onNextClick(false);
                          }}
                        >
                          Skip
                          <img src={arrowright} className="ms-2" alt="" />
                        </button>
                      ) : (
                          <button
                            className="btn btn-primary text-nowrap btn-sm"
                            onClick={() => {
                              onNextClick(true);
                            }}
                          >
                            Next
                            <img src={arrowright} class="ms-2" alt="" />
                          </button>
                        )}
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

SelectInterestScreen.propTypes = {
  props: PropTypes,
};

const mapStateToProps = (state) => ({});

const mapDispatchToProps = {
  registerUserDetails,
  getInterests,
  changeSelection,
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(SelectInterestScreen);
