import React from "react";
import { connect, useSelector } from "react-redux";
import { useHistory } from "react-router-dom";
// Image
import logo from "./../../assets/images/logo.svg";
// API Call
import { invokeResendEmail } from "../../redux/actions/SignupAction";
import { initiateLogin } from "./../../redux/actions/LoginAction";

export const VerifyScreen = (props) => {
  let history = useHistory();
  const signupState = useSelector((state) => state.signupState);

  //API call for Continue Button click
  const handleContinue = () => {
    let loginPostData = signupState.signupUser;
    props.initiateLogin(loginPostData, history, (user) => {
      if (user) {
        if (user.profile_status === "INACTIVE") {
          history.push("/select-user-role");
        } else if (
          user.profile_status === "ACTIVE" &&
          !user.is_skipimage &&
          (!user.isBackgroundImageUploaded || !user.isProfileImageUploaded)
        ) {
          history.push("/upload-shots");
        } else {
          history.push("/");
        }
      }
    });
  };

  const handleLogin = () => {
    history.push("/");
  };

  //API call for Resend Email
  const resendEmail = async (values) => {
    let emailPostData = signupState.signupUser;
    let emailData = {
      email: emailPostData.email
    }
    props.invokeResendEmail(emailData, history);
  };

  return (
    <div className="w-100 h-100 position-fixed">
      <div className="w-100 h-100 d-flex overflow-auto">
        <div className="container-fluid">
          <div className="row h-100">
            <div className="col-sm-12">
              <div className="d-flex flex-column h-100">
                <div className="logo">
                  <img src={logo} className="mt-3" alt="" />
                </div>
                <div className="row h-100">
                  <div className="col-sm-6 pt-5 offset-sm-3 d-flex flex-column">
                    <h1 className="my-5 display-3">Let"s verify your info!</h1>
                    <div className="mt-5">
                      <p className="mb-4 text-secondary">
                        We just sent slid into your email inbox and sent you a
                        text! Click the link in either one to confirm your
                        contact info. If you cant find the email, it might be
                        in spam (its fine, were fine).
                      </p>
                      <p className="mb-4 text-secondary">
                        If you need us to resend the confirmation email,{" "}
                        <button
                          className="text-info bg-transparent border-0 px-0"
                          onClick={() => resendEmail()}
                        >
                          click here.
                        </button>
                      </p>
                      <p className="mb-5">
                        Once you verify your info, Click "Continue" below!
                      </p>
                    </div>
                    <div className="d-flex flex-column col-sm-4 offset-sm-4 my-auto">
                      <button
                        className="btn btn-primary"
                        onClick={() => handleContinue()}
                      >
                        Continue
                      </button>
                      <button
                        className="btn text-secondary"
                        onClick={() => handleLogin()}
                      >
                        Back to login
                      </button>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

const mapStateToProps = (state) => ({});

const mapDispatchToProps = {
  invokeResendEmail,
  initiateLogin,
};

export default connect(mapStateToProps, mapDispatchToProps)(VerifyScreen);
