import { S3_GET_IMAGE_URL, S3_GET_VIDEO_URL } from "./URL";
import { encode as btoa } from "base-64";

export const isEmptyObject = (object) => {
  return (
    object === undefined || object === null || Object.keys(object).length === 0
  );
};

export const getProfileImageOriginalUrl = (profileImage, deviceState) => {
  if (!profileImage) {
    return "";
  }
  const imageRequest = JSON.stringify({
    bucket: deviceState.s3ImageBucketName,
    key: deviceState.profileImageLocation + profileImage,
    filters: {
      format: "webp",
    },
  });
  return S3_GET_IMAGE_URL + btoa(imageRequest);
};

export const log = (value) => {
  return console.log(value);
};


export const getProfileImageUrl = (profileImage, deviceState) => {
  if (!profileImage) {
    return '';
  }
  const imageRequest = JSON.stringify({
    bucket: deviceState.s3ImageBucketName,
    key: deviceState.profileImageLocation + profileImage,
    edits: {
      resize: {
        fit: 'contain',
      },
    },
    filters: {
      // quality: 90,
      format: 'webp',
    },
  });
  return S3_GET_IMAGE_URL + btoa(imageRequest);
}

export const getProfileImageThumbnailUrl = (profileImage, deviceState) => {
  if (!profileImage) {
    return '';
  }
  const imageRequest = JSON.stringify({
    bucket: deviceState.s3ImageBucketName,
    key: deviceState.profileImageLocation + profileImage,
    edits: {
      resize: {
        width: 100,
        height: 100,
        fit: 'cover',
      },
    },
    filters: {
      // quality: 90,
      format: 'webp',
    },
  });
  return S3_GET_IMAGE_URL + btoa(imageRequest);
}

