// Convert the date formate into  PST
export const getDate = (date) => {
  var localDate = new Date(date);
  if (!date) {
    return '';
  }
  let updateddate = localDate.toLocaleString('en-US', {
    timeZone: 'America/Los_Angeles',
  });
  return updateddate;
}