import React from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";

export const SenderChat = (props) => {
  return (
    <div class="bubble-set d-flex pb-2 flex-row-reverse">
      <div class="msg-text">
        <div class="bubble-text-purple bg-purple text-white lh-sm">
          Oh my gosh YES I would absolutely love to do a clothing haul with you.
          Ive been so excited about this line. Do you mind if I post on
          Tuesdays at 5:30? Thats usually my best time with these kinds of
          videos. Thank YOU!
        </div>
        <div class="text-details text-end mt-1">
          <span class="text-light-50">2:26 PM</span>
        </div>
      </div>
    </div>
  );
};

SenderChat.propTypes = {
  props: PropTypes,
};

const mapStateToProps = (state) => ({});

const mapDispatchToProps = {};

export default connect(mapStateToProps, mapDispatchToProps)(SenderChat);
