import Constants from "./../../utils/Constants";

const { ACTIONS } = Constants;

let initialState = {
  discoverList: {},
  discoverSelectedList: {},
  isDiscoverPageLoading: false,
  isDiscoverdetailsPageLoading: false,
};

export const discoverState = (state = initialState, action) => {
  const { type, discoverList, discoverSelectedList } = action;
  switch (type) {
    case ACTIONS.GET_DISCOVER_LIST:
      return { ...state, discoverList: action.payload };
    case ACTIONS.SHOW_DISCOVER_SCREEN_LOADING:
      return { ...state, isDiscoverPageLoading: true };
    case ACTIONS.HIDE_DISCOVER_SCREEN_LOADING:
      return { ...state, isDiscoverPageLoading: false };
    case ACTIONS.GET_DISCOVER_SELECTED_LIST:
      return { ...state, discoverSelectedList: action.payload };
    case ACTIONS.SHOW_DISCOVER_DETAILS_SCREEN_LOADING:
      return { ...state, isDiscoverdetailsPageLoading: true };
    case ACTIONS.HIDE_DISCOVER_DETAILS_SCREEN_LOADING:
      return { ...state, isDiscoverdetailsPageLoading: false };
    default:
      return state;
  }
};
