import Constants from "./../../utils/Constants";

const { ACTIONS } = Constants;

let initialState = {
  interestArray: [],
  isInterestLoading: false,
};

export const registerState = (state = initialState, action) => {
  const { type, interestArray, isInterestLoading } = action;

  switch (type) {
    case ACTIONS.GET_USER_INTERESTS:
      return { ...state, interestArray, isInterestLoading: false };
    case ACTIONS.SHOW_INTEREST_LOADING:
      return { ...state, isInterestLoading: true };
    case ACTIONS.HIDE_INTEREST_LOADING:
      return { ...state, isInterestLoading: false };
    default:
      return state;
  }
};
