import Constants from "./../../utils/Constants";
import HttpBaseClient from "./../../network/HttpBaseClient";
import { GET_NOTIFICATION_DATA_LIST } from "./../../utils/URL";
import Toaster from "./../../utils/Toaster/Toast";

export const getNotificationlist = (notificationPostData) => {
  return (dispatch) => {
    dispatch(showNotificationLoading());
    HttpBaseClient.get(GET_NOTIFICATION_DATA_LIST, notificationPostData, 1)
      .then((response) => {
        dispatch(hideNotificationLoading());
        if (response.code === 0) {
          console.log(response);
          dispatch({
            type: Constants.ACTIONS.GET_NOTIFICATION_LIST,
            payload: response.data,
          });
        } else if (response.code === 2) {
          Toaster.error(response.message);
        } else {
          Toaster.error(Constants.VALIDATION_MSG.REQ_FAILED);
        }
      })
      .catch((error) => {
        dispatch(hideNotificationLoading());
        // dispatch(handleError(error));
      });
  };
};

export const showNotificationLoading = () => {
  return (dispatch) => {
    dispatch({
      type: Constants.ACTIONS.SHOW_NOTIFICATION_SCREEN_LOADING,
    });
  };
};

export const hideNotificationLoading = () => {
  return (dispatch) => {
    dispatch({
      type: Constants.ACTIONS.HIDE_NOTIFICATION_SCREEN_LOADING,
    });
  };
};
