import React from "react";
import { connect } from "react-redux";
import SideMenu from "../../components/SideMenu";
//Images and icons
import subscription from "./../../assets/images/subscription.svg";
import favorites from "./../../assets/images/favorites.svg";
import instagram from "./../../assets/images/instagram.svg";
import terms from "./../../assets/images/terms.svg";

export const BrandSettingsScreen = () => {
  return (
    <div className="w-100 h-100 position-fixed">
      <div className="d-flex h-100">
        <SideMenu />
        <div className="w-100">
          <div className="container-fluid h-100">
            <div className="row h-100 mobile-overflow">
              <div className="col-md-8 pt-5 pb-3 h-desktop-100">
                <div className="row h-100">
                  <div className="col-lg-10 offset-lg-1">
                    <div className="d-flex">
                      <div>
                        <h1 className="fw-normal mb-0 display-3">settings</h1>
                      </div>
                    </div>
                    <div className="h-100 mt-3 overflow-auto">
                      <div className="d-flex border-bottom">
                        <div className="py-5 pe-4">
                          <img src={subscription} alt="" className="img-fluid" />
                        </div>
                        <div className="py-5">
                          <h4 className="mb-0">my subscription</h4>
                        </div>
                        <div className="ms-auto align-self-center">
                          <i className="far fa-chevron-right text-light"></i>
                        </div>
                      </div>
                      <div className="d-flex border-bottom">
                        <div className="py-5 pe-4">
                          <img src={favorites} alt="" className="img-fluid" />
                        </div>
                        <div className="py-5">
                          <h4 className="mb-0">favorites</h4>
                        </div>
                        <div className="ms-auto align-self-center">
                          <i className="far fa-chevron-right text-light"></i>
                        </div>
                      </div>
                      <div className="d-flex border-bottom">
                        <div className="py-5 pe-4">
                          <img src={instagram} alt="" className="img-fluid" />
                        </div>
                        <div className="py-5">
                          <h4 className="mb-0">social media accounts</h4>
                        </div>
                        <div className="ms-auto align-self-center">
                          <i className="far fa-chevron-right text-light"></i>
                        </div>
                      </div>
                      <div className="d-flex border-bottom">
                        <div className="py-5 pe-4">
                          <img src={terms} alt="" className="img-fluid" />
                        </div>
                        <div className="py-5">
                          <h4 className="mb-0">terms of use</h4>
                        </div>
                        <div className="ms-auto align-self-center">
                          <i className="far fa-chevron-right text-light"></i>
                        </div>
                      </div>
                      <div className="d-flex border-bottom">
                        <div className="py-5 pe-4">
                          <img src={terms} alt="" className="img-fluid" />
                        </div>
                        <div className="py-5">
                          <h4 className="mb-0">privacy policy</h4>
                        </div>
                        <div className="ms-auto align-self-center">
                          <i className="far fa-chevron-right text-light"></i>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

const mapStateToProps = (state) => ({});

const mapDispatchToProps = {};

export default connect(mapStateToProps, mapDispatchToProps)(BrandSettingsScreen);
