import Toaster from "../../../utils/Toaster/Toast";

export const downloandImage = (value) => {
  fetch(value, {
    method: "GET",
    headers: {},
  })
    .then((response) => {
      response.arrayBuffer().then(function (buffer) {
        const url = window.URL.createObjectURL(new Blob([buffer]));
        const link = document.createElement("a");
        link.href = url;
        link.setAttribute("download", "Embassy.png"); //or any other extension
        document.body.appendChild(link);
        link.click();
      });
      Toaster.success("Download Successfully");
    })
    .catch((err) => {
      console.log(err);
    });
};
