import React, { useEffect, useState } from "react";
import { connect, useSelector } from "react-redux";
import { Formik } from "formik";
import ReactPaginate from "react-paginate";
import * as Yup from "yup";
import Toaster from "../../utils/Toaster/Toast";
import Select from "react-select";

//Common Component
import SideMenu from "../../components/SideMenu";
import PageLoader from "../../components/PageLoader";
//Image
import coverphoto from "./../../assets/images/bg-photo.svg";
import shareImage from "./../../assets/images/share-grey.svg";
//API Call
import {
  getDiscoverlist,
  getDiscoverdetailslist,
} from "../../redux/actions/discover/DiscoverActions";
import { getProfileImageOriginalUrl } from "../../utils/Utility";

export const DiscoverScreen = (props) => {
  const params = {
    searchText: "",
  };
  const discoverState = useSelector((state) => state.discoverState);
  const deviceState = useSelector((state) => state.deviceState);

  const discoverListItem = discoverState.discoverList;
  const discoverDetailslistitem = discoverState.discoverSelectedList;

  const [locationArray, setLocationArray] = useState([]);

  //Pagenation useState
  const [offset, setOffSet] = useState(1);
  const [perPage, setPerPage] = useState(10);
  const [currentPage, setCurrentPage] = useState(0);
  //Search useState
  const [searchDiscoverItem, setSearchDiscoverItem] = useState("");

  // Discover details screen enable useState
  const [discoverDetailsscreen, setDiscoverDetailsscreen] = useState(false);

  // Discover popup screen enable useState
  const [discoverFilterscreen, setDiscoverFilterscreen] = useState(true);

  // Discover instagram and direct content check
  const [instaDirectcontentshow, setInstaDirectcontentshow] = useState(true);

  useEffect(() => {
    let searchfilterData = {
      age_from: 0,
      age_to: 100,
      gender: "All",
      address: "All",
      followerscount: "ALL",
      engagementrate_from: 0,
      engagementrate_to: 100,
      interest: "All",
      rating_from: 0,
      rating_to: 5,
    };

    let discoverPostData = {
      search_keyword: searchDiscoverItem,
      user_role: "INFLUENCER",
      ...searchfilterData,
      search_user_role: "INFLUENCER",
      offset: 1,
      limit: 10,
    };

    getDiscoverListapi(discoverPostData);
  }, []);

  //Pagination On click
  const handlePageClick = (e) => {
    const offset = e.selected * perPage;
    const limit = perPage;

    let searchfilterData = {
      age_from: 0,
      age_to: 100,
      gender: "All",
      address: "All",
      followerscount: "ALL",
      engagementrate_from: 0,
      engagementrate_to: 100,
      interest: "All",
      rating_from: 0,
      rating_to: 5,
    };

    let discoverPostData = {
      search_keyword: searchDiscoverItem,
      user_role: "INFLUENCER",
      ...searchfilterData,
      search_user_role: "INFLUENCER",
      offset: offset,
      limit: limit,
    };

    getDiscoverListapi(discoverPostData);
  };

  // Discover API call
  const getDiscoverListapi = (discoverPostData) => {
    props.getDiscoverlist(discoverPostData);
  };

  // Form validation
  const formValidationSchema = Yup.object().shape({});

  // Search onClick event call
  const handleSubmit = async (values) => {
    setDiscoverFilterscreen(true);
    const limit = perPage;

    let filterBrandData = {
      age_from: 0,
      age_to: 100,
      gender: "All",
      address: "All",
      followerscount: "ALL",
      engagementrate_from: 0,
      engagementrate_to: 100,
      interest: "All",
      rating_from: 0,
      rating_to: 5,
    };

    let discoverPostData = {
      search_keyword: values.searchText,
      user_role: "INFLUENCER",
      ...filterBrandData,
      search_user_role: "INFLUENCER",
      offset: offset,
      limit: limit,
    };

    if (values.searchText.length <= 2) {
      if (!isNaN(values.searchText)) {
        if (values.searchText.length <= 2) {
          setSearchDiscoverItem(values.searchText);
          getDiscoverListapi(discoverPostData);
        }
      } else {
        if (values.searchText.length >= 3) {
          setSearchDiscoverItem(values.searchText);
          getDiscoverListapi(discoverPostData);
        } else {
          Toaster.error("please enter max 3 value");
        }
      }
    } else if (values.searchText.length >= 3) {
      if (!isNaN(values.searchText)) {
        Toaster.error("please enter max 2 value for age");
      } else {
        setSearchDiscoverItem(values.searchText);
        getDiscoverListapi(discoverPostData);
      }
    } else {
      Toaster.error("-------error-----");
    }
  };

  // onClick Discover list its enable the  details screen
  const discoverDetails = (value) => {
    setDiscoverDetailsscreen(true);
    setInstaDirectcontentshow(true);

    let discoverdetailsPostData = {
      flag: "POST",
      isWeb: false,
      skip: "",
      user_id: value._id,
    };
    props.getDiscoverdetailslist(discoverdetailsPostData);
  };

  //onClick Close the discover details screen
  const closeDiscoverdetails = () => {
    setDiscoverDetailsscreen(false);
  };

  //onClick Open filter popup

  const openDiscoverfilter = () => {
    if (discoverFilterscreen === true) {
      setDiscoverFilterscreen(false);
    } else {
      setDiscoverFilterscreen(true);
    }
  };

  // onClick insta and direct content show and hide
  const openinstaDirectcontent = () => {
    if (instaDirectcontentshow === true) {
      setInstaDirectcontentshow(false);
    } else {
      setInstaDirectcontentshow(true);
    }
  };

  const locationStyles = {
    control: (styles) => ({
      ...styles,
      backgroundColor: "#10183e",
      borderColor: "#888c9f",
      lineHeight: 2,
    }),
    option: (styles) => ({ ...styles, color: "#10183e" }),
    singleValue: (styles) => ({ ...styles, color: "#fff" }),
  };

  return (
    <div class="w-100 h-100 position-fixed">
      <div class="d-flex h-100">
        <SideMenu />
        <div class="w-100">
          <div class="container-fluid h-100">
            <div class="row h-100 mobile-overflow">
              <div class="col-xl-8 pt-5 pb-3 h-desktop-100">
                <div class="row h-100">
                  <div class="col-lg-10 offset-lg-1 h-100 d-flex flex-column">
                    <h1 class="fw-normal display-5 mb-3">discover</h1>
                    <div class="filter-search">
                      <div class="bg-blur border-blur rounded-4 pt-2 pb-2 px-xl-4 px-2">
                        <Formik
                          initialValues={{ ...params }}
                          validationSchema={formValidationSchema}
                          onSubmit={handleSubmit}
                          enableReinitialize={true}
                        >
                          {({
                            values,
                            touched,
                            errors,
                            handleSubmit,
                            handleChange,
                          }) => {
                            return (
                              <form onSubmit={handleSubmit}>
                                <div class="d-flex">
                                  <i class="pl-2 h5 mb-0 far fa-search align-self-center"></i>

                                  <input
                                    name="searchText"
                                    id="searchText"
                                    type="text"
                                    class="form-control border-0 bg-transparent"
                                    placeholder="Search Creator..."
                                    value={values.searchText}
                                    onChange={handleChange}
                                  />

                                  <a
                                    class="btn d-flex align-items-center"
                                    onClick={() => {
                                      openDiscoverfilter();
                                    }}
                                  >
                                    <i class="h5 mb-0 fal fa-filter align-self-center me-2"></i>{" "}
                                    Filter
                                  </a>
                                  <button class="btn btn-primary rounded-4 py-xl-3 px-xl-5">
                                    Search
                                  </button>
                                </div>
                              </form>
                            );
                          }}
                        </Formik>

                        <div
                          class={
                            discoverFilterscreen
                              ? "row mt-3 mb-4 d-none"
                              : "row mt-3 mb-4 "
                          }
                          onclick="location.href='discover-filter.html'"
                        >
                          <div class="col-12">
                            <hr class="mt-0" />
                          </div>
                          <div class="col-md-6">
                            <div class="mb-3">
                              <label class="form-label spacer">AGE</label>
                              <select class="form-select form-control">
                                <option value="">All</option>
                              </select>
                            </div>
                          </div>
                          <div class="col-md-6">
                            <div class="mb-3">
                              <label class="form-label spacer">SEX</label>
                              <select class="form-select form-control">
                                <option value="">All</option>
                              </select>
                            </div>
                          </div>
                          <div class="col-md-12">
                            <div class="mb-3">
                              <label class="form-label spacer">
                                LOCATION(S)
                              </label>
                              <div class="input-group mb-3">
                                <input
                                  type="text"
                                  class="form-control"
                                  placeholder="All"
                                />
                                <span class="input-group-text">
                                  <i class="far fa-search"></i>
                                </span>
                              </div>
                            </div>
                          </div>
                          <div class="col-md-12">
                            <div class="mb-3">
                              <label class="form-label spacer">
                                CATEGORIES
                              </label>

                              <div class="input-group mb-3">
                                <Select
                                  styles={locationStyles}
                                  classNamePrefix="search.."
                                  name="address"
                                  //value={values.address}
                                  //  isLoading={locationLoading}
                                  options={locationArray}
                                  placeholder="Search locations.."
                                  //  onInputChange={handleInputChange}
                                  onChange={(event) => {
                                    // setFieldValue("address", event);
                                  }}
                                  theme={(theme) => ({
                                    ...theme,
                                    borderRadius: 0,
                                    lineHeight: 2,
                                    colors: {
                                      ...theme.colors,
                                      primary: "lightgrey",
                                    },
                                  })}
                                />
                                {/* <input
                                  type="text"
                                  class="form-control"
                                  placeholder="All"
                                /> */}
                                <span class="input-group-text mb-1">
                                  <i class="far fa-search"></i>
                                </span>
                              </div>
                            </div>
                          </div>
                          <div class="col-md-6">
                            <div class="mb-3">
                              <label class="form-label spacer">FOLLOWERS</label>
                              <select class="form-select form-control">
                                <option value="">All</option>
                              </select>
                            </div>
                          </div>
                          <div class="col-md-6">
                            <div class="mb-3">
                              <label class="form-label spacer">
                                ENGAGEMENT
                              </label>
                              <select class="form-select form-control">
                                <option value="">All</option>
                              </select>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>

                    {discoverState.isDiscoverPageLoading ? (
                      <PageLoader
                        isLoading={discoverState.isDiscoverPageLoading}
                        isLoggedin={true}
                      />
                    ) : (
                      <div class="row pb-3 underneath-d desktop-overflow">
                        <div class="row clear-row-m"></div>

                        {discoverListItem.length
                          ? discoverListItem.map((value, index) => {
                              return (
                                <div class="col-xl-3 col-md-4 col-sm-4">
                                  <div
                                    class="profile-card"
                                    onClick={() => {
                                      discoverDetails(value);
                                    }}
                                  >
                                    <img
                                      src={
                                        value.background_photo
                                          ? getProfileImageOriginalUrl(
                                              value.background_photo,
                                              deviceState
                                            )
                                          : coverphoto
                                      }
                                    />
                                    <div class="profile-name mb-3">
                                      <div class="profile-img align-self-center">
                                        <img
                                          src={
                                            value.background_photo
                                              ? getProfileImageOriginalUrl(
                                                  value.photo,
                                                  deviceState
                                                )
                                              : coverphoto
                                          }
                                        />
                                      </div>
                                      <h6 class="mb-0 align-self-center">
                                        {value.first_name} <br />
                                        {value.last_name}
                                      </h6>
                                    </div>
                                  </div>
                                </div>
                              );
                            })
                          : null}
                      </div>
                    )}

                    <div>
                      {" "}
                      <ReactPaginate
                        previousLabel={"prev"}
                        nextLabel={"next"}
                        breakLabel={"..."}
                        breakClassName={"break-me"}
                        pageCount={5}
                        marginPagesDisplayed={3}
                        pageRangeDisplayed={10}
                        onPageChange={handlePageClick}
                        containerClassName={"pagination"}
                        subContainerClassName={"pages pagination"}
                        activeClassName={"active"}
                      />
                    </div>
                  </div>
                </div>
              </div>

              {discoverDetailsscreen ? (
                <div class="col-xl-4 pt-5 pb-3 h-desktop-100 desktop-overflow pe-xl-5">
                  {discoverState.isDiscoverdetailsPageLoading ? (
                    <PageLoader
                      isLoading={discoverState.isDiscoverdetailsPageLoading}
                      isLoggedin={true}
                    />
                  ) : (
                    <div class="profile-details">
                      <div class="d-flex justify-content-between position-absolute full-screen w-100">
                        <button class="btn btn-sm bg-blur small px-4">
                          FULL PROFILE
                        </button>
                        <button
                          class="btn btn-sm rounded-circle bg-blur small me-n3"
                          onClick={() => {
                            closeDiscoverdetails();
                          }}
                        >
                          <i class="far fa-times ps-1"></i>
                        </button>
                      </div>
                      {/* //discoverDetailslistitem */}
                      <div class="profile-card">
                        <img
                          src={
                            discoverDetailslistitem.background_photo
                              ? getProfileImageOriginalUrl(
                                  discoverDetailslistitem.background_photo,
                                  deviceState
                                )
                              : coverphoto
                          }
                        />
                        <div class="profile-name mb-5">
                          <div class="messenger-profile align-self-center me-2 ms-3">
                            <img
                              src={
                                discoverDetailslistitem.background_photo
                                  ? getProfileImageOriginalUrl(
                                      discoverDetailslistitem.photo,
                                      deviceState
                                    )
                                  : coverphoto
                              }
                            />
                          </div>
                          <div class="p-2 align-self-center me-2">
                            <h3 class="mb-0">
                              {discoverDetailslistitem.first_name} <br />
                              {discoverDetailslistitem.last_name}
                            </h3>
                            <p class="mb-0 small text-white">
                              {discoverDetailslistitem &&
                                discoverDetailslistitem.city != null &&
                                (discoverDetailslistitem.city + ", " || "")}
                              {""}
                              {discoverDetailslistitem &&
                                discoverDetailslistitem.state != null &&
                                (discoverDetailslistitem.state || "")}
                              {discoverDetailslistitem &&
                              discoverDetailslistitem.brand_name
                                ? null
                                : discoverDetailslistitem.age}
                            </p>
                          </div>
                          <span class="mt-2 bg-white rounded-3 align-self-start text-dark small ms-auto me-3 px-2 text-nowrap">
                            <i class="fa fa-star text-warning"></i>{" "}
                            {(discoverDetailslistitem &&
                              parseFloat(
                                discoverDetailslistitem.rating
                              ).toFixed(1)) ||
                              0.0}
                          </span>
                        </div>
                      </div>
                      <div class="rounded-4 p-4 bg-white mt-n4 z-index text-dark">
                        <div class="d-flex justify-content-end">
                          {" "}
                          <button class="btn rounded-circle btn-primary mt-n5 shadow">
                            <span class="far fa-plus"></span>
                          </button>
                        </div>

                        <div class="d-flex justify-content-start flex-wrap">
                          {discoverDetailslistitem
                            ? discoverDetailslistitem.interests
                              ? discoverDetailslistitem.interests.map(
                                  (value, index) => {
                                    return (
                                      <div class="mt-1">
                                        <span class="badge badge-light small p-1 text-dark">
                                          {value.interest_name}
                                        </span>
                                      </div>
                                    );
                                  }
                                )
                              : null
                            : null}
                        </div>

                        <p class="text-black mt-3">
                          {discoverDetailslistitem.description}
                        </p>
                        <div class="mt-3 d-flex text-dark">
                          <h1
                            class={
                              instaDirectcontentshow === true
                                ? "border-bottom-2 border-info me-3"
                                : " me-3"
                            }
                          >
                            <i
                              onClick={() => {
                                openinstaDirectcontent();
                              }}
                              class={
                                instaDirectcontentshow === true
                                  ? "fab fa-instagram fa-xs text-primary"
                                  : "fab fa-instagram fa-xs"
                              }
                            ></i>
                          </h1>
                          <h2
                            class={
                              instaDirectcontentshow === false
                                ? "border-bottom-2 border-info me-3"
                                : " me-3"
                            }
                          >
                            <img
                              onClick={() => {
                                openinstaDirectcontent();
                              }}
                              class={
                                instaDirectcontentshow === false
                                  ? "fab fa-instagram fa-xs text-primary"
                                  : null
                              }
                              src={shareImage}
                            />
                          </h2>
                        </div>
                        <h6 class="text-black">
                          <b>my analytics</b>
                        </h6>
                        <div class="d-flex justify-content-between">
                          <div>
                            <h1 class="mb-0 fw-normal">
                              {discoverDetailslistitem &&
                                discoverDetailslistitem.instagram_basic_display &&
                                (discoverDetailslistitem.instagram_basic_display
                                  .media_count ||
                                  0)}
                            </h1>
                            <p class="small tetx-black">Posts</p>
                          </div>
                          <div>
                            <h1 class="mb-0 fw-normal">
                              {discoverDetailslistitem &&
                                discoverDetailslistitem.instagram_basic_display &&
                                (discoverDetailslistitem.instagram_basic_display
                                  .followers_count ||
                                  0)}
                            </h1>
                            <p class="small text-black">Followers</p>
                          </div>
                          <div>
                            <h1 class="mb-0 fw-normal">
                              {discoverDetailslistitem &&
                                discoverDetailslistitem.analytics &&
                                (discoverDetailslistitem.analytics
                                  .engagement_rate ||
                                  0)}{" "}
                              %
                            </h1>
                            <p class="small text-black">Engagement</p>
                          </div>
                        </div>
                        <h6 class="mt-3 mb-3 text-black">
                          <b>my rates</b>
                        </h6>

                        {instaDirectcontentshow ? (
                          discoverDetailslistitem.ig ? (
                            discoverDetailslistitem.ig.map((value, index) => {
                              return (
                                <div class="d-flex rounded shadow justify-content-between overflow-hidden mb-3">
                                  <div class="p-4 align-self-center">
                                    <p class="mb-0">
                                      <i class="fab fa-instagram me-1"></i>
                                      {value.name === "VIDEO"
                                        ? value.name + " post"
                                        : value.name === "POST"
                                        ? "photo " + value.name
                                        : value.name}
                                    </p>
                                  </div>
                                  {value.name !== "CUSTOM REQUEST" ? (
                                    <div class="btn-primary p-4 rounded-end-start">
                                      {Math.round(value.total_amount)}
                                    </div>
                                  ) : null}
                                </div>
                              );
                            })
                          ) : (
                            <div class="p-4 align-self-center">
                              <p> No rates found</p>
                            </div>
                          )
                        ) : discoverDetailslistitem.dc ? (
                          discoverDetailslistitem.dc.map((value, index) => {
                            return (
                              <div class="d-flex rounded shadow justify-content-between overflow-hidden mb-3">
                                <div class="p-4 align-self-center">
                                  <p class="mb-0">
                                    <i class="fab fa-instagram"></i>
                                    {value.name === "VIDEO"
                                      ? value.name + " post"
                                      : value.name === "POST"
                                      ? "photo " + value.name
                                      : value.name}
                                  </p>
                                </div>
                                <div class="btn-primary p-4 rounded-end-start">
                                  {Math.round(value.total_amount)}
                                </div>
                              </div>
                            );
                          })
                        ) : (
                          <div class="p-4 align-self-center">
                            <p> No direct content created yet</p>
                          </div>
                        )}
                      </div>
                    </div>
                  )}
                </div>
              ) : null}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

const mapStateToProps = (state) => ({});

const mapDispatchToProps = {
  getDiscoverlist,
  getDiscoverdetailslist,
};

export default connect(mapStateToProps, mapDispatchToProps)(DiscoverScreen);
