import Constants from "./../../utils/Constants";
import HttpBaseClient from "./../../network/HttpBaseClient";
import { GET_PERFORMANCE_DATA_LIST } from "./../../utils/URL";
import Toaster from "./../../utils/Toaster/Toast";

export const getPerformanceList = (performancePostData) => {
  return (dispatch) => {
    dispatch(showPerformanceLoading());
    HttpBaseClient.post(GET_PERFORMANCE_DATA_LIST, performancePostData, 1)
      .then((response) => {
        dispatch(hidePerformanceLoading());
        if (response.code === 0) {
          dispatch({
            type: Constants.ACTIONS.GET_PERFORMANCE_LIST,
            payload: response.data,
          });
        } else if (response.code === 2) {
          Toaster.error(response.message);
        } else {
          Toaster.error(Constants.VALIDATION_MSG.REQ_FAILED);
        }
      })
      .catch((error) => {
        dispatch(hidePerformanceLoading());
        // dispatch(handleError(error));
      });
  };
};

export const showPerformanceLoading = () => {
  return (dispatch) => {
    dispatch({
      type: Constants.ACTIONS.SHOW_PERFORMANCE_SCREEN_LOADING,
    });
  };
};

export const hidePerformanceLoading = () => {
  return (dispatch) => {
    dispatch({
      type: Constants.ACTIONS.HIDE_PERFORMANCE_SCREEN_LOADING,
    });
  };
};
