import React from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";
//Common Components
import ReceiverChat from "./ReceiverChat";
import SenderChat from "./SenderChat";
//image
import send from "../assets/images/send.svg";
import clip from "../assets/images/clip.svg";
import image1 from "../assets/images/img1(2).png";

const conversationArray = [
  { type: "receiver" },
  { type: "sender" },
  { type: "receiver" },
  { type: "sender" },
  { type: "receiver" },
];
export const ChatBox = (props) => {
  // renders the conversation list between the sender and receiver
  const renderConversationList = () => {
    let tempArr = [];
    conversationArray.map((item, index) => {
      if (item.type === "receiver") {
        tempArr.push(<ReceiverChat />);
      } else {
        tempArr.push(<SenderChat />);
      }
    });
    return tempArr;
  };

  return (
    <div class="col-lg-5 pt-5 pb-3 mt-5 h-desktop-100">
      <div class="row h-100">
        <div class="col-lg-10 offset-lg-1 h-100 d-flex flex-column">
          <div class="d-flex">
            <div class="d-flex py-4 flex-fill bd-highlight border-bottom">
              <div class="messenger-profile align-self-center me-4">
                <img src={image1} alt="" class="img-fluid" />
              </div>
              <div class="align-self-center flex-grow-1">
                <h4 class="mb-1">
                  Kendric <br />
                  Jackson
                </h4>
              </div>
            </div>
          </div>
          <div class="row py-5 overflow-auto h-100">
            <div class="col-12">
              <div class="chat-single d-flex flex-column h-100 bd-highlight">
                <div class="msg-area flex-grow-1 pr-1 flex-fill bd-highlight h-100 overflow-auto">
                  <div class="d-flex flex-column align-self-end w-100">
                    {renderConversationList()}
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="text-area d-flex mb-5 flex-fill bd-highlight border border-2">
            <div class="text-box reply">
              <textarea
                class="text-box border-0 bg-transparent flex-grow-1 pl-3"
                rows="1"
                placeholder="Write your message"
              ></textarea>
            </div>
            <button class="btn btn-outline-primary rounded-circle-1 me-1">
              <img src={clip} alt="" class="clip-btn mb-2 w-50" />
            </button>
            <button class="btn-primary rounded-circle-1">
              <img src={send} alt="" class="send-btn mb-3" />
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

ChatBox.propTypes = {
  props: PropTypes,
};

const mapStateToProps = (state) => ({});

const mapDispatchToProps = {};

export default connect(mapStateToProps, mapDispatchToProps)(ChatBox);
