import Constants from "./../../../utils/Constants";
import HttpBaseClient from "./../../../network/HttpBaseClient";
import { GET_CALENDAR_DETAILS } from "./../../../utils/URL";
import { handleError } from "../NetworkAction";
import Toaster from "../../../utils/Toaster/Toast";
import { log } from "../../../utils/Utility";

export const getCalendarList = (
  fromDate,
  toDate,
  isInfluencer,
  type,
  currentDate
) => {
  return (dispatch) => {
    dispatch(showCalendarLoading());
    HttpBaseClient.post(
      GET_CALENDAR_DETAILS,
      { fromDate, toDate, isInfluencer, type, currentDate },
      1
    )
      .then((response) => {
        if (response.code === 0) {
          log("------>", response);
          dispatch(hideCalendarLoading());
        } else {
          Toaster.error(response.message);
        }
      })
      .catch((error) => {
        dispatch(hideCalendarLoading());
        dispatch(handleError(error));
      });
  };
};

export const showCalendarLoading = () => {
  return (dispatch) => {
    dispatch({
      type: Constants.ACTIONS.SHOW_CALENDAR_LOADING,
    });
  };
};

export const hideCalendarLoading = () => {
  return (dispatch) => {
    dispatch({
      type: Constants.ACTIONS.HIDE_CALENDAR_LOADING,
    });
  };
};
