import React from "react";
import { connect } from "react-redux";
import { Link, useHistory } from "react-router-dom";
// Common Component
import Stepper from "../../components/Stepper";
// Image
import logo from "./../../assets/images/logo.svg";
import arrowleft from "./../../assets/images/arrow-left.svg";
import arrowright from "./../../assets/images/arrow-right.svg";
import tick from "./../../assets/images/tick.svg";

export const SocialLoginSuccess = () => {
  const history = useHistory();

  const navigateDashboard = () => {
    history.push("/brand-home");
  };

  return (
    <div className="w-100 h-100 position-fixed">
      <div className="w-100 h-100 d-flex overflow-auto">
        <div className="container-fluid">
          <div className="row h-100">
            <div className="col-sm-12">
              <div className="d-flex flex-column h-100">
                <div className="logo">
                  <img src={logo} className="mt-3" alt="" />
                </div>
                <div className="row h-100">
                  <div className="col-sm-6 pt-4 offset-sm-3 d-flex flex-column all-contents">
                    <p className="text-muted small fw-bold mb-1"> 7 / 7</p>
                    <Stepper stepperActive={6} />
                    <h1 className="mt-5 pb-5 display-3">Woohoo!</h1>
                    <div className="">
                      <p className="text-secondary">
                        Youre good to go! Its time to get after it. Lets go
                        find some new creators and grow your brand!
                      </p>
                    </div>
                    <div className="d-flex signup-photos">
                      <div className="d-flex py-3 flex-fill bd-highlight">
                        <div className="align-self-center me-4">
                          <i className="fab fa-instagram fa-lg"></i>
                        </div>
                        <div className="profile-msg align-self-center flex-grow-1">
                          <h5 className="mb-0 text-white display-6">
                            Sign in with instagram
                          </h5>
                        </div>
                      </div>
                      <div className="list-inline align-self-center text-center">
                        <img src={tick} alt="" />
                      </div>
                    </div>
                    <div className="d-flex justify-content-between mb-5 mt-auto">
                      <Link
                        to="/select-user-role"
                        className="text-white text-nowrap align-self-center"
                      >
                        <img src={arrowleft} alt="" className="me-2" />
                        Back
                      </Link>
                      <button
                        className="btn btn-primary text-nowrap btn-sm"
                        onClick={() => navigateDashboard()}
                      >
                        Next
                        <img src={arrowright} className="ms-2" alt="" />
                      </button>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
const mapStateToProps = (state) => ({});

const mapDispatchToProps = {};

export default connect(mapStateToProps, mapDispatchToProps)(SocialLoginSuccess);
