import React, { useState } from "react";
import { connect } from "react-redux";
import { Link, useHistory } from "react-router-dom";
// Image
import logo from "./../../assets/images/logo.svg";
import arrowleft from "./../../assets/images/arrow-left.svg";
import arrowright from "./../../assets/images/arrow-right.svg";
// API Call
import { addBioDescriptionDetails } from "./../../redux/actions/SignupAction";
// Common Component
import Stepper from "../../components/Stepper";

export const AddBioScreen = (props) => {
  const [description, setDescription] = useState("");
  const history = useHistory();

  // On Click API call
  const handleSubmit = () => {
    if (description !== "") {
      let descriptionPostData = {
        description: description,
      };
      props.addBioDescriptionDetails(descriptionPostData, history);
    } else {
      history.push("/connect-social-login");
    }
  };

  return (
    <div className="w-100 h-100 position-fixed">
      <div className="w-100 h-100 d-flex overflow-auto">
        <div className="container-fluid">
          <div className="row h-100">
            <div className="col-sm-12">
              <div className="d-flex flex-column h-100">
                <div className="logo">
                  <img src={logo} className="mt-3" alt="" />
                </div>
                <div className="row h-100">
                  <div className="col-sm-6 pt-4 offset-sm-3 d-flex flex-column all-contents">
                    <p className="text-muted small fw-bold mb-1">5/7</p>
                    <Stepper stepperActive={4} />
                    <h1 className="mt-5 pb-5 display-3">
                      Finish your profile?
                    </h1>
                    <form>
                      <div className="mb-3">
                        <label className="form-label">Bio</label>
                        <textarea
                          name="description"
                          onChange={(e) => {
                            setDescription(e.target.value);
                          }}
                          value={description}
                          className="form-control"
                          id="exampleFormControlTextarea1"
                          rows="5"
                          placeholder="Write something short and memorable! This is so brands can get a quick glimpse of your personality and what kind of creator you are."
                        ></textarea>
                      </div>
                    </form>
                    <div className="d-flex justify-content-between mb-5 mt-auto">
                      <Link
                        to="/upload-shots"
                        className="text-white text-nowrap align-self-center"
                      >
                        <img src={arrowleft} alt="" className="me-2" />
                        Back
                      </Link>
                      {description === "" ? (
                        <button
                          className="btn btn-outline-primary text-nowrap btn-sm"
                          onClick={handleSubmit}
                        >
                          {" "}
                          Skip
                          <img src={arrowright} className="ms-2" alt="" />
                        </button>
                      ) : (
                          <button
                            className="btn btn-primary text-nowrap btn-sm"
                            onClick={handleSubmit}
                          >
                            {" "}
                          Next
                            <img src={arrowright} className="ms-2" alt="" />
                          </button>
                        )}
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

const mapStateToProps = (state) => ({});

const mapDispatchToProps = {
  addBioDescriptionDetails,
};

export default connect(mapStateToProps, mapDispatchToProps)(AddBioScreen);
