import React, { useEffect } from "react";
import { connect, useSelector } from "react-redux";
import ReactPaginate from "react-paginate";
//Common Component
import SideMenu from "./../../components/SideMenu";
import PageLoader from "./../../components/PageLoader";
import BrandOrderListItem from "../../components/BrandOrderListItem";
// API Call
import { getCollaborationList } from "./../../redux/actions/collaborations/CollaborationAction";

export const BrandCollabListScreen = (props) => {

  useEffect(() => {
    requestedCollaborationList();
    inprogressCollaborationList();
    completedCollaborationList();
  }, []);

  const requestedCollaborationList = () => {
    let requestedParams = {
      offset: 1,
      limit: 10,
      status: "REQUESTED"
    };
    props.getCollaborationList(requestedParams);
  }

  const inprogressCollaborationList = () => {
    let inprogressParams = {
      offset: 1,
      limit: 10,
      status: "INPROGRESS"
    };
    props.getCollaborationList(inprogressParams);
  }

  const completedCollaborationList = () => {
    let completedParams = {
      offset: 1,
      limit: 10,
      status: "COMPLETED"
    };
    props.getCollaborationList(completedParams);
  }

  // From Reducer
  const collabortionState = useSelector((state) => state.collabortionState);
  const requestedCollaborationsList = collabortionState.requestedCollaborationsList;
  const inprogressCollaborationsList = collabortionState.inprogressCollaborationsList;
  const completedCollaborationsList = collabortionState.completedCollaborationsList;

  const handlePageClick = (e) => {
    console.log(e.selected * 10, "++++++++")
  }

  const renderBrandCollabList = () => {
    if (collabortionState.isCollabListLoading) {
      return <PageLoader isLoading={collabortionState.isCollabListLoading} isLoggedin={true} />
    } else {
      return (
        <div className="container-fluid h-100">
          <div className="row h-100 mobile-overflow">
            <div className="col-md-12 pt-5 pb-3 h-desktop-100">
              <div className="row">
                <div className="col-lg-4 pb-5 offset-lg-1">
                  <h1 className="fw-normal mb-0 display-3">my collabs</h1>
                </div>
              </div>
              <div className="row h-100">
                <div className="col-lg-3 h-desktop-100 offset-lg-1 d-flex flex-column">
                  <div className="p-3 rounded-1 border-blur bg-blur mb-2">
                    <div className="d-xl-flex justify-content-evenly text-center text-lg-start">
                      <div>
                        <div className="circle-outline circle-info mx-auto">
                          <h1 className="d-table-cell align-middle text-center text-white fw-normal">
                            {requestedCollaborationsList && requestedCollaborationsList.orderCount ? requestedCollaborationsList.orderCount : 0}
                          </h1>
                        </div>
                        <div className="align-self-center flex-grow-1 text-center">
                          <p className="mt-2 text-white text-uppercase spacer"><b>Requested</b></p>
                        </div>
                      </div>
                      <div className="list-inline border-start border-2">
                        <div className="ms-lg-4 mb-3">
                          <p className="small mb-1 text-secondary text-uppercase spacer">Total pending</p>
                          <h2 className="fw-normal">
                            <sup>$</sup>
                            {requestedCollaborationsList && requestedCollaborationsList.overAllAmount ? requestedCollaborationsList.overAllAmount : 0}
                          </h2>
                        </div>
                        <div className="ms-lg-4">
                          <p className="mb-1 small text-secondary text-uppercase spacer ">This Month</p>
                          <h2 className="mb-0 fw-normal">
                            <sup>$</sup>
                            {requestedCollaborationsList && requestedCollaborationsList.thisMonthAmount ? requestedCollaborationsList.thisMonthAmount : 0}
                          </h2>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="h-100 underneath overflow-auto">
                    <div className="row clear-row"></div>
                    {requestedCollaborationsList && requestedCollaborationsList.orderList && requestedCollaborationsList.orderList.length ? (
                      requestedCollaborationsList.orderList.map((collaboration, index) => {
                        return (
                          <BrandOrderListItem collaborationItem={collaboration} key={index} />
                        );
                      })
                    ) :
                      <div className="text-center">
                        <p>No records found</p>
                      </div>
                    }
                    <div className="row clear-row d-lg-block d-none"></div>
                  </div>
                </div>
                <div className="vr mx-4 px-0 d-lg-block d-none"></div>
                <div className="col-lg-3 h-desktop-100 d-flex flex-column">
                  <div className="p-3 rounded-1 border-blur bg-blur mb-2">
                    <div className="d-xl-flex justify-content-evenly text-center text-lg-start">
                      <div>
                        <div className="circle-outline circle-warning mx-auto">
                          <h1 className="d-table-cell align-middle text-center text-white fw-normal">
                            {inprogressCollaborationsList && inprogressCollaborationsList.orderCount ? inprogressCollaborationsList.orderCount : 0}
                          </h1>
                        </div>
                        <div className="align-self-center flex-grow-1 text-center">
                          <p className="mt-2 text-white text-uppercase spacer"><b>in progress</b></p>

                        </div>
                      </div>
                      <div className="list-inline border-start border-2">
                        <div className="ms-4 mb-3">
                          <p className="small mb-1 text-secondary text-uppercase spacer">Total pending</p>
                          <h2 className="fw-normal">
                            <sup>$</sup>
                            {inprogressCollaborationsList && inprogressCollaborationsList.overAllAmount ? inprogressCollaborationsList.overAllAmount : 0}
                          </h2>
                        </div>
                        <div className="ms-4">
                          <p className="mb-1 small text-secondary text-uppercase spacer ">This Month</p>
                          <h2 className="mb-0 fw-normal">
                            <sup>$</sup>
                            {inprogressCollaborationsList && inprogressCollaborationsList.thisMonthAmount ? inprogressCollaborationsList.thisMonthAmount : 0}
                          </h2>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="h-100 underneath overflow-auto">
                    <div className="row clear-row"></div>
                    {inprogressCollaborationsList && inprogressCollaborationsList.orderList && inprogressCollaborationsList.orderList.length ? (
                      inprogressCollaborationsList.orderList.map((collaboration, index) => {
                        return (
                          <BrandOrderListItem collaborationItem={collaboration} key={index} />
                        );
                      })
                    ) :
                      <div className="text-center">
                        <p>No records found</p>
                      </div>
                    }
                    <div className="row clear-row d-lg-block d-none"></div>
                  </div>
                </div>
                <div className="vr mx-4 px-0 d-lg-block d-none"></div>
                <div className="col-lg-3 h-desktop-100 d-flex flex-column">
                  <div className="p-3 rounded-1 border-blur bg-blur mb-2">
                    <div className="d-xl-flex justify-content-evenly text-center text-lg-start">
                      <div>
                        <div className="circle-outline circle-success mx-auto">
                          <h1 className="d-table-cell align-middle text-center text-white fw-normal">
                            {completedCollaborationsList && completedCollaborationsList.orderCount ? completedCollaborationsList.orderCount : 0}
                          </h1>
                        </div>
                        <div className="align-self-center flex-grow-1 text-center">
                          <p className="mt-2 text-white text-uppercase spacer"><b>completed</b></p>

                        </div>
                      </div>
                      <div className="list-inline border-start border-2">
                        <div className="ms-4 mb-3">
                          <p className="small mb-1 text-secondary text-uppercase spacer">Total pending</p>
                          <h2 className="fw-normal">
                            <sup>$</sup>
                            {completedCollaborationsList && completedCollaborationsList.overAllAmount ? completedCollaborationsList.overAllAmount : 0}
                          </h2>
                        </div>
                        <div className="ms-4">
                          <p className="mb-1 small text-secondary text-uppercase spacer ">This Month</p>
                          <h2 className="mb-0 fw-normal">
                            <sup>$</sup>
                            {completedCollaborationsList && completedCollaborationsList.thisMonthAmount ? completedCollaborationsList.thisMonthAmount : 0}
                          </h2>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="h-100 underneath overflow-auto">
                    <div className="row clear-row"></div>
                    {completedCollaborationsList && completedCollaborationsList.orderList && completedCollaborationsList.orderList.length ? (
                      completedCollaborationsList.orderList.map((collaboration, index) => {
                        return (
                          <BrandOrderListItem collaborationItem={collaboration} key={index} />
                        );
                      })

                    ) :
                      <div className="text-center">
                        <p>No records found</p>
                      </div>
                    }
                    <div className="row clear-row d-lg-block d-none"></div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      )
    }
  }

  return (
    <div className='w-100 h-100 position-fixed'>
      <div className='d-flex h-100'>
        <SideMenu />
        <div className="w-100">
          {renderBrandCollabList()}
        </div>
      </div>
    </div >
  );
};

const mapStateToProps = (state) => ({});

const mapDispatchToProps = {
  getCollaborationList
};

export default connect(mapStateToProps, mapDispatchToProps)(BrandCollabListScreen);
