import React, { useEffect, useState } from "react";
import PropTypes from "prop-types";
import { connect, useSelector } from "react-redux";
import moment from "moment";

// Common components
import SideMenu from "../../components/SideMenu";
import BrandOrderListItem from "../../components/BrandOrderListItem";
import PageLoader from "../../components/PageLoader";

import { getCalendarList } from "../../redux/actions/calendar/CalendarAction";
// image
import Swet from "../../assets/images/swet-hot.svg";
import image1 from "../../assets/images/img(1).png";
import profileFive from "./../../assets/images/sample/kendric.svg";

export const CalendarScreen = (props) => {
  const calendarState = useSelector((state) => state.calendarState);

  const [startDate, setStartDate] = useState(
    moment().clone().startOf("month").format("YYYY-MM-DD")
  );
  const [endDate, setEndDate] = useState(
    moment().clone().endOf("month").format("YYYY-MM-DD")
  );
  const [currentDate, setCurrentDate] = useState(moment().format("YYYY-MM-DD"));

  useEffect(() => {
    props.getCalendarList(startDate, endDate, false, true, currentDate);
  }, []);

  const calendarDetail = {
    today: [
      { name: "Kendric Jackson", photo: profileFive },
      { name: "Kendric Jackson", photo: profileFive },
    ],
    comingSoon: [
      { name: "Kendric Jackson", photo: profileFive },
      { name: "Kendric Jackson", photo: profileFive },
      { name: "Kendric Jackson", photo: profileFive },
      { name: "Kendric Jackson", photo: profileFive },
      { name: "Kendric Jackson", photo: profileFive },
      { name: "Kendric Jackson", photo: profileFive },
      { name: "Kendric Jackson", photo: profileFive },
      { name: "Kendric Jackson", photo: profileFive },
    ],
  };

  const renderTodayList = () => {
    let todayArray = [];
    calendarDetail.today.map((item, index) => {
      todayArray.push(<BrandOrderListItem item={item} index={index} />);
    });
    return todayArray;
  };

  const renderComingSoon = () => {
    let comingSoonArr = [];
    calendarDetail.comingSoon.map((item, index) => {
      comingSoonArr.push(<BrandOrderListItem item={item} index={index} />);
    });
    return comingSoonArr;
  };

  const renderCalender = () => {
    return (
      <div class="calendar-table">
        <table>
          <thead>
            <tr>
              <th>sun</th>
              <th>mon</th>
              <th>tue</th>
              <th>wed</th>
              <th>thu</th>
              <th>fri</th>
              <th>sat</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td>
                <div class="day-tile text-muted">26</div>
              </td>
              <td>
                <div class="day-tile text-muted">27</div>
              </td>
              <td>
                <div class="day-tile text-muted">28</div>
              </td>
              <td>
                <div class="day-tile text-muted">29</div>
              </td>
              <td>
                <div class="day-tile text-muted">30</div>
              </td>
              <td>
                <div class="day-tile active">1</div>
                <div class="d-flex my-1 flex-nowrap">
                  <img src={Swet} alt="" />
                  <p class="mb-0 small text-nowrap text-truncate align-self-center text-secondary">
                    Lorem Ipsummm
                  </p>
                </div>
                <div class="d-flex my-1 flex-nowrap">
                  <img src={Swet} alt="" />
                  <p class="mb-0 small text-nowrap text-truncate align-self-center text-secondary">
                    Lorem Ipsummm
                  </p>
                </div>
                <div class="d-flex my-1 flex-nowrap">
                  <p class="mb-0 small text-nowrap text-truncate align-self-center text-secondary">
                    +2 more
                  </p>
                </div>
              </td>
              <td>
                <div class="day-tile">2</div>
              </td>
            </tr>
            <tr>
              <td>
                <div class="day-tile">3</div>
              </td>
              <td>
                <div class="day-tile">4</div>
              </td>
              <td>
                <div class="day-tile">5</div>
              </td>
              <td>
                <div class="day-tile">6</div>
              </td>
              <td>
                <div class="day-tile">7</div>
              </td>
              <td>
                <div class="day-tile">8</div>
              </td>
              <td>
                <div class="day-tile">9</div>
              </td>
            </tr>
            <tr>
              <td>
                <div class="day-tile">10</div>
              </td>
              <td>
                <div class="day-tile">11</div>
              </td>
              <td>
                <div class="day-tile">12</div>
              </td>
              <td>
                <div class="day-tile">13</div>
              </td>
              <td>
                <div class="day-tile">14</div>
              </td>
              <td>
                <div class="day-tile">15</div>
              </td>
              <td>
                <div class="day-tile">16</div>
              </td>
            </tr>
            <tr>
              <td>
                <div class="day-tile inactive">17</div>
                <div class="d-flex my-1 flex-nowrap">
                  <img src={image1} alt="" />
                  <p class="mb-0 small text-nowrap text-truncate align-self-center text-secondary">
                    Lorem Ipsummm
                  </p>
                </div>
              </td>
              <td>
                <div class="day-tile">18</div>
              </td>
              <td>
                <div class="day-tile">19</div>
              </td>
              <td>
                <div class="day-tile">20</div>
              </td>
              <td>
                <div class="day-tile">21</div>
              </td>
              <td>
                <div class="day-tile">22</div>
              </td>
              <td>
                <div class="day-tile">23</div>
              </td>
            </tr>
            <tr>
              <td>
                <div class="day-tile">24</div>
              </td>
              <td>
                <div class="day-tile">25</div>
              </td>
              <td>
                <div class="day-tile">26</div>
              </td>
              <td>
                <div class="day-tile">27</div>
              </td>
              <td>
                <div class="day-tile">28</div>
              </td>
              <td>
                <div class="day-tile">29</div>
              </td>
              <td>
                <div class="day-tile">30</div>
              </td>
            </tr>
            <tr>
              <td>
                <div class="day-tile">31</div>
              </td>
              <td>
                <div class="day-tile text-muted">1</div>
              </td>
              <td>
                <div class="day-tile text-muted">2</div>
              </td>
              <td>
                <div class="day-tile text-muted">3</div>
              </td>
              <td>
                <div class="day-tile text-muted">4</div>
              </td>
              <td>
                <div class="day-tile text-muted">5</div>
              </td>
              <td>
                <div class="day-tile text-muted">6</div>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    );
  };

  const renderScreen = () => {
    if (calendarState.isCalendarLoading) {
      return <PageLoader isLoading={calendarState.isCalendarLoading} />;
    } else {
      return (
        <div class="w-100">
          <div class="container-fluid h-100">
            <div class="row h-100 mobile-overflow mb-3">
              <div class="col-md-8 pt-5 h-desktop-100 overflow-auto">
                <div class="row h-100">
                  <div class="col-lg-10 offset-lg-1 h-100 d-flex flex-column mt-2">
                    <h1 class="fw-normal mb-3 display-3">calendar</h1>
                    <div class="d-flex justify-content-between">
                      <h4 class="mb-0 fw-normal">October 2021</h4>
                      <div class="d-flex align-self-center">
                        <span class="fal fa-chevron-circle-left me-1 fa-lg"></span>
                        <span class="fal fa-chevron-circle-right fa-lg"></span>
                      </div>
                    </div>
                    {renderCalender()}
                  </div>
                </div>
              </div>
              <div class="col-md-4 pt-5 h-desktop-100 desktop-overflow mt-md-5">
                <div class="row h-100 mt-md-3">
                  <div class="col-lg-10 offset-lg-1 h-100 d-flex flex-column">
                    <div>
                      <h4 class="mb-3 fw-normal">Tuesday, October 19</h4>
                      {renderTodayList()}
                      <h4 class="mb-3 fw-normal">coming soon</h4>
                      {renderComingSoon()}
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      );
    }
  };

  return (
    <div class="w-100 h-100 position-fixed">
      <div class="d-flex h-100">
        <SideMenu />
        {renderScreen()}
      </div>
    </div>
  );
};

CalendarScreen.propTypes = {
  props: PropTypes,
};

const mapStateToProps = (state) => ({});

const mapDispatchToProps = { getCalendarList };

export default connect(mapStateToProps, mapDispatchToProps)(CalendarScreen);
