import React, { useEffect, useState } from "react";
import { connect, useSelector } from "react-redux";
import moment from "moment";
import ReactPaginate from "react-paginate";
//Common Component
import SideMenu from "./../../components/SideMenu";
import PageLoader from "../../components/PageLoader";
//API Call
import { getPerformanceList } from "./../../redux/actions/PerformanceAction";
import { downloandImage } from "../../redux/actions/common/DownloadAction";
//Image
import heartImage from "./../../assets/images/heart.svg";
import chatImage from "./../../assets/images/chat.svg";
import viewImage from "./../../assets/images/view.svg";
import commentImage from "./../../assets/images/comment.svg";
import leftArrowImage from "./../../assets/images/arrow-left.svg";

export const PerformanceScreen = (props) => {
  const performanceState = useSelector((state) => state.performanceState);
  const performanceListItem = performanceState.performanceList;
  const [perPage] = useState(10);

  const [performancePopup, setPerformancePopup] = useState(false);
  const [performanceDetails, setPerformanceDetails] = useState({});

  useEffect(() => {
    let performancePostData = {
      offset: 1,
      limit: 10,
    };

    getPerformanceList(performancePostData);
  }, []);

  const getPerformanceList = (performancePostData) => {
    props.getPerformanceList(performancePostData);
  };

  const handlePageClick = (e) => {
    const offset = e.selected * perPage;
    const limit = perPage;
    let performancePostData = {
      offset: offset,
      limit: limit,
    };
    getPerformanceList(performancePostData);
  };

  const performanceUser = (value) => {
    setPerformancePopup(true);
    setPerformanceDetails(value);
  };
  const backPerformance = () => {
    setPerformancePopup(false);
  };

  const downloandContent = (value) => {
    downloandImage(value);
  };

  const instagramDetails = (value) => {
    window.open("https://www.instagram.com/p/" + value);
  };

  return (
    <div class="w-100 h-100 position-fixed">
      <div class="d-flex h-100">
        <SideMenu />
        <div class="w-100">
          {performancePopup ? (
            <div class="advanced-search">
              <div class="row d-flex justify-content-center mt-5 pt-4 h-100 overflow-auto">
                <div
                  class="col-xl-1 col-2 mb-4"
                  onClick={() => {
                    backPerformance();
                  }}
                >
                  <a class="text-white text-nowrap align-self-center">
                    <img src={leftArrowImage} class="me-2" alt="" /> Back
                  </a>
                </div>
                <div class="col-xl-3 col-6 my-4 offset-md-2">
                  <div class="d-flex mb-3">
                    <div class="performance-profile align-self-center me-4">
                      <img
                        src={performanceDetails.media[0].media_url}
                        alt=""
                        class="img-fluid"
                      />
                    </div>
                    <div>
                      <h4 class="mb-0">
                        {" "}
                        {performanceDetails.created_by.first_name}{" "}
                        {" " + performanceDetails.created_by.last_name}
                      </h4>
                      <div class="d-xl-flex small text-secondary">
                        <i class="align-self-center fal fa-camera me-1"></i>
                        <small class="align-self-center">
                          {" "}
                          {performanceDetails.orderDetails.service_type ===
                            "STORY"
                            ? "Story"
                            : performanceDetails.type === "VIDEO"
                              ? "Video Post"
                              : "Photo Post"}
                        </small>
                      </div>
                    </div>
                  </div>
                  <div class="parent-card mb-3">
                    <div class="card text-white bg-white border-0">
                      <img
                        class="card-image-performance-details"
                        src={performanceDetails.media[0].media_url}
                        alt=""
                      />
                    </div>
                  </div>
                  <div class="d-flex align-self-center justify-content-between pb-2">
                    <span class="text-light">
                      <img src={heartImage} class=" me-1" alt="" />
                      {performanceDetails.total_likes}
                    </span>
                    <span class="text-light">
                      <img src={chatImage} class=" me-1" alt="" />
                      {performanceDetails.total_comments}
                    </span>
                    <span class="text-light">
                      <img src={viewImage} class=" me-1" alt="" />
                      {performanceDetails.total_impression}
                    </span>
                    <span class="text-light">
                      <img src={commentImage} class=" me-1" alt="" />
                      {performanceDetails.total_saved}
                    </span>
                  </div>
                  <div class="row mt-5">
                    <button
                      class="btn btn-primary w-100"
                      onClick={() => {
                        downloandContent(performanceDetails.media[0].media_url);
                      }}
                    >
                      download content
                    </button>
                    <button
                      class="btn btn-outline-light w-100 mt-2"
                      onClick={() => {
                        instagramDetails(performanceDetails.shortcode);
                      }}
                    >
                      view on instagram
                    </button>
                    <button
                      class="btn btn-outline-light w-100 mt-2"
                      onClick={() => {
                        //  collabDetails();
                      }}
                    >
                      view collab details
                    </button>
                  </div>
                </div>
              </div>
            </div>
          ) : null}

          <div class="container-fluid h-100">
            <div class="row h-100 mobile-overflow">
              <div class="col-md-12 pt-5 pb-3 h-desktop-100 desktop-overflow">
                <div class="row border-end h-100">
                  <div class="col-lg-10 offset-lg-1">
                    <div class="d-flex pb-2">
                      <div>
                        <h1 class="fw-normal mb-0 display-3">performance</h1>
                      </div>
                    </div>
                    {performanceState.isPerformancePageLoading ? (
                      <PageLoader
                        isLoading={performanceState.isPerformancePageLoading}
                        isLoggedin={true}
                      />
                    ) : (
                        <div class="row mt-5 border-bottom  pagination-height align-items-center">
                          {performanceListItem.length ? (
                            performanceListItem.map((value, index) => {
                              let duration = moment.duration(
                                moment(new Date()).diff(value.created_at)
                              );

                              return (
                                <div
                                  class="col-xl-3 col-md-6 col-12 mb-4 pe-3 signup-photos"
                                  onClick={() => {
                                    performanceUser(value);
                                  }}
                                >
                                  <div class="parent-card mb-3">
                                    <div class="card text-white bg-white border-0">
                                      {value.orderDetails.service_type !==
                                        "STORY" ? (
                                          <img
                                            class="img-card-top"
                                            src={
                                              value.type === "IMAGE" ||
                                                value.type === "CAROUSEL_ALBUM"
                                                ? value.media[0].media_url
                                                : value.media[0].thumbnail_url
                                            }
                                            alt=""
                                          />
                                        ) : duration._data.days === 0 ? (
                                          <img
                                            class="img-card-top"
                                            src={
                                              value.type === "IMAGE"
                                                ? value.media[0].media_url
                                                : value.media[0].thumbnail_url
                                            }
                                            alt=""
                                          />
                                        ) : (
                                            <img
                                              class="img-card-top"
                                              src={
                                                value.type === "IMAGE"
                                                  ? value.media[0].media_url
                                                  : value.media[0].thumbnail_url
                                              }
                                              alt=""
                                            />
                                          )}
                                    </div>
                                  </div>
                                  <div class="d-flex mb-3">
                                    <div>
                                      <h4 class="mb-0">
                                        {" "}
                                        {value.created_by.first_name}{" "}
                                        {" " + value.created_by.last_name}
                                      </h4>
                                      <div class="d-xl-flex small text-secondary">
                                        <i class="align-self-center fal fa-camera me-1"></i>
                                        <small class="align-self-center">
                                          {value.orderDetails.service_type ===
                                            "STORY"
                                            ? "Story"
                                            : value.type === "VIDEO"
                                              ? "Video Post"
                                              : "Photo Post"}
                                        </small>
                                      </div>
                                    </div>
                                    <div class="ms-auto">
                                      <small class="text-secondary">
                                        {moment(value.created_at).format(
                                          "MM/DD/YYYY"
                                        )}
                                      </small>
                                    </div>
                                  </div>
                                  <div class="d-flex align-self-center justify-content-between pb-2">
                                    <span class="text-light small">
                                      <img src={heartImage} class=" me-1" />
                                      {value.total_likes}
                                    </span>
                                    <span class="text-light small">
                                      <img src={chatImage} class=" me-1" />
                                      {value.total_comments}
                                    </span>
                                    <span class="text-light small">
                                      <img src={viewImage} class=" me-1" />
                                      {value.total_impression}
                                    </span>
                                    <span class="text-light small">
                                      <img src={commentImage} class=" me-1" />
                                      {value.total_saved}
                                    </span>
                                  </div>
                                </div>
                              );
                            })
                          ) : (
                              <div class="text-center ">No record found</div>
                            )}
                        </div>
                      )}
                    <div>
                      {" "}
                      <ReactPaginate
                        previousLabel={"prev"}
                        nextLabel={"next"}
                        breakLabel={"..."}
                        breakClassName={"break-me"}
                        pageCount={5}
                        marginPagesDisplayed={3}
                        pageRangeDisplayed={10}
                        onPageChange={handlePageClick}
                        containerClassName={"pagination"}
                        subContainerClassName={"pages pagination"}
                        activeClassName={"active"}
                      />
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

const mapStateToProps = (state) => ({});

const mapDispatchToProps = {
  getPerformanceList,
  downloandImage,
};

export default connect(mapStateToProps, mapDispatchToProps)(PerformanceScreen);
