import Constants from "../../../utils/Constants";
import HttpBaseClient from "../../../network/HttpBaseClient";
import { GET_PROFILE_DETAILS } from "../../../utils/URL";
import { handleError } from "../NetworkAction";
import Toaster from "../../../utils/Toaster/Toast";

/**
 * To get userProfile details
 * input param user_id:string(optional)
 */
export const getUserProfileDetails = (
  userId,
  isFromPulltoRefresh,
  callback
) => {
  return (dispatch, getState) => {
    dispatch(showProfileDetialsLoading());
    let user_id = "";
    // if userId is present, then it is other users profile. we have to store the response in
    // otherProfileDetails
    if (userId) {
      user_id = userId;
    }
    HttpBaseClient.post(GET_PROFILE_DETAILS, { user_id: user_id }, 1)
      .then((response) => {
        dispatch(hideProfileDetailsLoading());
        if (response.code === 0) {
          if (user_id) {
            dispatch({
              type: Constants.ACTIONS.GET_OTHERS_PROFILE_DETAILS_SUCCESS,
              othersProfileDetails: response.data,
            });
          } else {
            dispatch({
              type: Constants.ACTIONS.GET_PROFILE_DETAILS_SUCCESS,
              userProfileDetails: response.data,
            });
          }
          if (callback) {
            callback();
          }
        } else {
          Toaster.error(response.message);
        }
      })
      .catch((error) => {
        dispatch(hideProfileDetailsLoading());
        dispatch(handleError(error));
      });
  };
};

/**
 * To show profileScreen loading
 */
export const showProfileDetialsLoading = () => {
  return (dispatch) => {
    dispatch({
      type: Constants.ACTIONS.SHOW_PROFILE_DETAILS_LOADING,
    });
  };
};

/**
 * To hide profileScreen loading
 */
export const hideProfileDetailsLoading = () => {
  return (dispatch) => {
    dispatch({
      type: Constants.ACTIONS.HIDE_PROFILE_DETAILS_LOADING,
    });
  };
};
