import React, { useEffect, useState } from "react";
import PropTypes from "prop-types";
import { connect, useSelector } from "react-redux";
import SideMenu from "../../components/SideMenu";
import moment from "moment";

import {
  getMenuList,
  getDashboardDetails,
} from "../../redux/actions/home/HomeAction";
import { getCalendarList } from "../../redux/actions/calendar/CalendarAction";

//images and icons
import dummyLogo from "./../../assets/images/sample/dummy-logo.svg";
import profileOne from "./../../assets/images/sample/profile-1.svg";
import profileTwo from "./../../assets/images/sample/profile-2.svg";
import profileThree from "./../../assets/images/sample/profile-3.svg";
import profileFour from "./../../assets/images/sample/profile-4.svg";
import profileFive from "./../../assets/images/sample/kendric.svg";
import collabs from "./../../assets/images/collabs.svg";
import contentIcon from "./../../assets/images/content.svg";

import BrandOrderListItem from "../../components/BrandOrderListItem";
import PageLoader from "../../components/PageLoader";

export const BrandHomeScreen = (props) => {
  const homeState = useSelector((state) => state.homeState);

  const [startDate, setStartDate] = useState(
    moment().clone().startOf("month").format("YYYY-MM-DD")
  );
  const [endDate, setEndDate] = useState(
    moment().clone().endOf("month").format("YYYY-MM-DD")
  );
  const [currentDate, setCurrentDate] = useState(moment().format("YYYY-MM-DD"));

  useEffect(() => {
    props.getMenuList("BRAND");
    props.getDashboardDetails("BRAND");
    props.getCalendarList(startDate, endDate, false, true, currentDate);
  }, []);

  const trendingUser = [
    { name: "Carly Hyde", photo: profileOne },
    { name: "Breanna Mitchell", photo: profileTwo },
    { name: "Avery Halloway", photo: profileThree },
    { name: "Nate Filmore", photo: profileFour },
    { name: "Carly Hyde", photo: profileOne },
    { name: "Nate Filmore", photo: profileTwo },
    { name: "Avery Halloway", photo: profileThree },
    { name: "Breanna Mitchell", photo: profileFour },
    { name: "Carly Hyde", photo: profileOne },
  ];

  const calendarDetail = {
    today: [
      { name: "Kendric Jackson", photo: profileFive },
      { name: "Kendric Jackson", photo: profileFive },
    ],
    comingSoon: [
      { name: "Kendric Jackson", photo: profileFive },
      { name: "Kendric Jackson", photo: profileFive },
      { name: "Kendric Jackson", photo: profileFive },
      { name: "Kendric Jackson", photo: profileFive },
      { name: "Kendric Jackson", photo: profileFive },
      { name: "Kendric Jackson", photo: profileFive },
      { name: "Kendric Jackson", photo: profileFive },
      { name: "Kendric Jackson", photo: profileFive },
    ],
  };

  const renderTrendingList = () => {
    let trendingArray = [];
    trendingUser.map((item, index) => {
      trendingArray.push(
        <div class="text-center me-4">
          <div class="user-dp">
            <img src={item.photo} />
          </div>
          <p class="mt-1 text-secondary mb-0 small text-nowrap">{item.name}</p>
        </div>
      );
    });
    return trendingArray;
  };

  const renderTodayList = () => {
    let todayArray = [];
    calendarDetail.today.map((item, index) => {
      todayArray.push(<BrandOrderListItem item={item} index={index} />);
    });
    return todayArray;
  };

  const renderComingSoon = () => {
    let comingSoonArr = [];
    calendarDetail.comingSoon.map((item, index) => {
      comingSoonArr.push(<BrandOrderListItem item={item} index={index} />);
    });
    return comingSoonArr;
  };

  const renderScreen = () => {
    if (homeState.isBrandDashboardLoading) {
      return <PageLoader isLoading={homeState.isBrandDashboardLoading} />;
    } else {
      return (
        <div class="w-100">
          <div class="container-fluid h-100">
            <div class="row h-100 mobile-overflow">
              <div class="col-md-8 pt-5 pb-3 h-desktop-100 desktop-overflow">
                <div class="row border-end h-100">
                  <div class="col-lg-10 offset-lg-1">
                    <div class="d-flex">
                      <div class="user-dp me-3">
                        <img src={dummyLogo} />
                      </div>
                      <div>
                        <h1 class="fw-normal mb-0 display-5">dashboard</h1>
                        <p class="text-light">Swet Hot Yoga</p>
                      </div>
                    </div>
                    <h4 class="mt-5 mb-3 fw-normal">suggested creators</h4>
                    <div class="blue-fade-right w-100">
                      <div class="d-flex overflow-auto">
                        {renderTrendingList()}
                      </div>
                    </div>

                    <h4 class="mt-5 mb-3 fw-normal">spending overview</h4>
                    <div class="d-flex justify-content-between overflow-auto">
                      <div>
                        <h1 class="fw-normal">$2,300</h1>
                        <p class="text-info spacer status-size">REQUESTED</p>
                      </div>
                      <div class="mx-3">
                        <h1 class="fw-normal">$5,600</h1>
                        <p class="text-warning spacer status-size">
                          IN PROGRESS
                        </p>
                      </div>
                      <div>
                        <h1 class="fw-normal">$15,590</h1>
                        <p class="text-success spacer status-size">COMPLETED</p>
                      </div>
                    </div>
                    <div class="row my-4 pb-1">
                      <div class="col-lg-6">
                        <div class="bg-dark p-4 rounded h-100 d-flex flex-column">
                          <div class="d-flex">
                            <a href="" class="text-white">
                              <img src={collabs} />
                            </a>
                          </div>
                          <h3 class="mt-4 fw-normal">collaborations</h3>
                          <p class="text-light">
                            Manage all your current requested,current, and
                            former partnerships.
                          </p>
                          <div class="d-flex justify-content-end mt-4">
                            <button
                              class="btn btn-primary rounded-circle"
                              onclick="location.href='collaborations.html'"
                            >
                              <i class="fal fa-long-arrow-right fa-2x"></i>
                            </button>
                          </div>
                        </div>
                      </div>
                      <div class="col-lg-6 mt-3 mt-lg-0">
                        <div class="bg-dark p-4 rounded h-100 d-flex flex-column">
                          <div class="d-flex">
                            <a href="" class="text-white content-icon">
                              <img src={contentIcon} />
                            </a>
                          </div>
                          <h3 class="mt-4 fw-normal">content</h3>
                          <p class="text-light">
                            View and download the sponsored content you
                            purchased from creators.
                          </p>
                          <div class="d-flex justify-content-end mt-4">
                            <button class="btn btn-primary rounded-circle">
                              <i class="fal fa-long-arrow-right fa-2x"></i>
                            </button>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div class="col-md-4 pt-5 pb-3 h-desktop-100">
                <div class="row h-100">
                  <div class="col-lg-10 offset-lg-1 h-100 d-flex flex-column">
                    <div class="p-3 rounded border bg-blur">
                      <div class="d-flex justify-content-between">
                        <p class="mb-0">
                          <b>November</b>
                        </p>
                        <div>
                          <span class="fal fa-chevron-circle-left"></span>
                          <span class="fal fa-chevron-circle-right"></span>
                        </div>
                      </div>
                      <div class="d-flex flex-nowrap mt-3 justify-content-between overflow-auto">
                        <div class="small text-center">
                          <p class="mb-0">Sun</p>
                          <button class="btn btn-sm rounded-circle text-primary">
                            31
                          </button>
                        </div>
                        <div class="small text-center">
                          <p class="mb-0">Mon</p>
                          <button class="btn btn-sm btn-primary rounded-circle">
                            1
                          </button>
                        </div>
                        <div class="small text-center">
                          <p class="mb-0">Tue</p>
                          <button class="btn btn-sm rounded-circle">2</button>
                        </div>
                        <div class="small text-center">
                          <p class="mb-0">Wed</p>
                          <button class="btn btn-sm rounded-circle">3</button>
                        </div>
                        <div class="small text-center">
                          <p class="mb-0">Thu</p>
                          <button class="btn btn-sm rounded-circle">4</button>
                        </div>
                        <div class="small text-center">
                          <p class="mb-0">Fri</p>
                          <button class="btn btn-sm rounded-circle">5</button>
                        </div>
                        <div class="small text-center">
                          <p class="mb-0">Sat</p>
                          <button class="btn btn-sm rounded-circle">6</button>
                        </div>
                      </div>
                    </div>
                    <div class="h-100 mt-3 overflow-auto">
                      <h4 class="mb-3 fw-normal">today</h4>
                      {renderTodayList()}
                      <h4 class="mb-3 fw-normal">coming soon</h4>
                      {renderComingSoon()}
                    </div>
                    <div class="col-sm-4 offset-sm-3 my-auto collabs">
                      <button class="btn btn-primary new-collabs-button">
                        start new collabs!
                      </button>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      );
    }
  };

  return (
    <div class="w-100 h-100 position-fixed">
      <div class="d-flex h-100">
        <SideMenu />
        {renderScreen()}
      </div>
    </div>
  );
};

BrandHomeScreen.propTypes = {
  props: PropTypes,
};

const mapStateToProps = (state) => ({});

const mapDispatchToProps = {
  getMenuList,
  getDashboardDetails,
  getCalendarList,
};

export default connect(mapStateToProps, mapDispatchToProps)(BrandHomeScreen);
