import Constants from "./../../utils/Constants";

let initialState = {
  isProfileDetailsLoading: false, // To show/hide profile screen loading
  userProfileDetails: {}, // To store userProfileDetails
  isProfileUpdateLoading: false, //To show edit profile screen loading
};

const {
  ACTIONS: {
    SHOW_PROFILE_DETAILS_LOADING,
    HIDE_PROFILE_DETAILS_LOADING,
    GET_PROFILE_DETAILS_SUCCESS,
    SHOW_PROFILE_DETAILS_UPDATE_LOADING,
    HIDE_PROFILE_DETAILS_UPDATE_LOADING,
    PROFILE_DETAILS_UPDATE_SUCCESS,
    LOGOUT_CLEAR_ALL_STATES,
  },
} = Constants;

export const profileState = (state = initialState, action) => {
  const { type, userProfileDetails } = action;
  switch (type) {
    case SHOW_PROFILE_DETAILS_LOADING:
      return { ...state, isProfileDetailsLoading: true };
    case HIDE_PROFILE_DETAILS_LOADING:
      return { ...state, isProfileDetailsLoading: false };
    case GET_PROFILE_DETAILS_SUCCESS:
      return {
        ...state,
        userProfileDetails,
        isProfileDetailsLoading: false,
        isProfileDetailsPulltoRefreshLoading: false,
      };
    case SHOW_PROFILE_DETAILS_UPDATE_LOADING:
      return { ...state, isProfileUpdateLoading: true };
    case HIDE_PROFILE_DETAILS_UPDATE_LOADING:
      return { ...state, isProfileUpdateLoading: false };
    case PROFILE_DETAILS_UPDATE_SUCCESS:
      return { ...state, userProfileDetails, isProfileUpdateLoading: false };
    case LOGOUT_CLEAR_ALL_STATES:
      return { ...initialState };
    default:
      return state;
  }
};
