import React from "react";
import { connect, useSelector } from "react-redux";
import { Link } from "react-router-dom";
// Image
import logo from "./../assets/images/logo.svg";
import logoIcon from "./../assets/images/logo-icon.svg";
import dummyLogo from "./../assets/images/sample/dummy-logo.svg";
import { log } from "../utils/Utility";

const SideMenu = () => {
  // From Reducer
  const homeState = useSelector((state) => state.homeState);
  const brandMenu = homeState.brandMenu;

  return (
    <div className="side-navigation">
      <div className="p-3">
        <img src={logo} className="d-lg-block d-none" alt="" />
        <img src={logoIcon} className="d-lg-none d-md-block" alt="" />
      </div>
      <div className="d-lg-flex justify-content-end d-none">
        <div
          className="collapse-btn"
          onClick={() => {
            log("clicked");
          }}
        >
          <span className="fal fa-angle-left"></span>
        </div>
      </div>
      <div className="h-100 d-flex flex-column overflow-x-auto">
        <div className="navigation">
          {brandMenu && brandMenu.length
            ? brandMenu.map((menu, index) => {
              let menuClass = "nav-icon" + " " + menu.menu_icon;
              return (
                <Link
                  to={menu.route}
                  key={index}
                  className={
                    menu.route === window.location.pathname ? "active" : ""
                  }
                >
                  <div className={menuClass}></div>
                  {menu.menu_name}
                </Link>
              );
            })
            : ""}
        </div>
        <div className="navigation mt-auto">
          <Link to="/">
            <div className="nav-icon">
              <img src={dummyLogo} alt="" />
            </div>
            Swet Hot Yoga
          </Link>
          <Link to="/brand-settings">
            <div className="nav-icon settings"></div>
            settings
          </Link>
          <Link to="/">
            <div className="nav-icon logout"></div>
            logout
          </Link>
        </div>
      </div>
    </div>
  );
};

const mapStateToProps = (state) => ({});

const mapDispatchToProps = {};

export default connect(mapStateToProps, mapDispatchToProps)(SideMenu);
