import Constants from "./../../utils/Constants";
import Toaster from "./../../utils/Toaster/Toast";

/**
 * Checks the internet connection and sets the status in the state of the store
 */
export const handleError = (error, showAlert = true) => {
  return (dispatch, getState) => {
    try {
      if (error) {
        if (
          error.status === Constants.HTTP_CODE.AUTHENTICATION_FAILURE ||
          error.status === Constants.HTTP_CODE.REQUIRED_MISSING
        ) {
          Toaster.error("Session Expired");
        } else {
          if (showAlert) {
            if (error.status === 400) {
              Toaster.error(error.message);
            } else if (
              !getState().deviceState.isNetworkConnectivityAvailable ||
              !getState().deviceState.isInternetReachable
            ) {
              Toaster.error(Constants.VALIDATION_MSG.NO_INTERNET);
            } else if (error.status && error.status === 422) {
              Toaster.error("Permission denied.");
            } else if (error.message) {
              if (error.message.includes("Network Error")) {
                Toaster.error("No Internet");
              } else if (error.message.includes("timeout of")) {
                Toaster.error("Time Out");
              } else if (error.message.length > 10) {
                Toaster.error(error.message);
              } else {
                Toaster.error("Request Faild");
              }
            } else {
              Toaster.error("Request Faild");
            }
          }
        }
      } else {
        alert(Constants.VALIDATION_MSG.NO_INTERNET);
      }
    } catch (e) {
      alert(Constants.VALIDATION_MSG.WENT_WRONG);
    }
  };
};
