import Constants from "./../../utils/Constants";
import HttpBaseClient from "./../../network/HttpBaseClient";
import { OAUTH, REFRESH_TOKEN, GET_S3_CONFIG } from "./../../utils/URL";
import { handleError } from "./NetworkAction";
import Toaster from "./../../utils/Toaster/Toast";
import store from "store";

export const initiateLogin = (loginParams, history, callBack) => {
  return (dispatch) => {
    dispatch(showLoginLoading());
    HttpBaseClient.post(OAUTH, loginParams, 3)
      .then((response) => {
        dispatch(hideLoginLoading());
        if (response.code === 0) {
          let userRolesDetails = {
            url: response.data.user.url,
            redirect_url: response.data.user.redirect_url,
          };
          dispatch({
            type: Constants.ACTIONS.LOGIN_SUCCESS,
            loginUserData: response.data,
          });
          store.set("user", response.data);
          dispatch(getS3Config());
          dispatch(updateUrl(userRolesDetails));
          callBack(response.data.user);
        } else if (response.code === 2) {
          Toaster.success(response.messaage);
        } else if (response.code === 3) {
          Toaster.success(response.message);
          let data = {
            email: loginParams.email,
            password: loginParams.password,
            device: {},
          };
          dispatch({
            type: Constants.ACTIONS.SET_SIGNUP_USER,
            payload: data,
          });
          history.push("/verify");
        } else {
          Toaster.error("login failed");
        }
      })
      .catch((error) => {
        dispatch(hideLoginLoading());
        dispatch(handleError(error));
      });
  };
};

export const showLoginLoading = () => {
  return (dispatch) => {
    dispatch({
      type: Constants.ACTIONS.SHOW_LOGIN_LOADING,
    });
  };
};

export const hideLoginLoading = () => {
  return (dispatch) => {
    dispatch({
      type: Constants.ACTIONS.HIDE_LOGIN_LOADING,
    });
  };
};

