// update url and redirect url  From Login Response
export const updateUrl = (userRolesDetails) => {
  return (dispatch) => {
    dispatch({
      type: Constants.ACTIONS.GET_USER_ROLES_SUCCESS,
      userRolesDetails: userRolesDetails,
    });
  };
};

export const getS3Config = () => {
  return (dispatch) => {
    HttpBaseClient.get(GET_S3_CONFIG, {}, 1)
      .then((s3response) => {
        if (s3response.code === 0) {
          dispatch({
            type: Constants.ACTIONS.UPDATE_S3_BUCKET_DETAILS,
            s3ImageBucketName: s3response.data.image_bucket,
            s3videoBucketName: s3response.data.video_bucket,
            s3postImageLocation: s3response.data.image_location.post_image,
            profileImageLocation: s3response.data.image_location.profile,
            s3storyImageLocation: s3response.data.image_location.story_image,
            s3campaignImageLocation: s3response.data.image_location.campaign,
            s3postVideoLocation: s3response.data.video_location.post_video,
            s3storyVideoLocation: s3response.data.video_location.story_video,
            s3thumbnailLocation: s3response.data.image_location.thumb_nail,
            s3documentLoaction: s3response.data.image_location.document,
          });
        } else {
        }
      })
      .catch((error) => {
        dispatch(handleError(error));
      });
  };
};
