import Constants from "./../../utils/Constants";

const { ACTIONS } = Constants;

let initialState = {
  isCalendarLoading: false,
};

export const calendarState = (state = initialState, action) => {
  const { type } = action;
  switch (type) {
    case ACTIONS.SHOW_CALENDAR_LOADING:
      return { ...state, isCalendarLoading: true };
    case ACTIONS.HIDE_CALENDAR_LOADING:
      return { ...state, isCalendarLoading: false };
    case ACTIONS.LOGOUT_CLEAR_ALL_STATES:
      return { ...initialState };
    default:
      return state;
  }
};
