import Constants from "./../../utils/Constants";
import HttpBaseClient from "./../../network/HttpBaseClient";
import {
  SIGNUP,
  RESEND_EMAIL,
  GET_USER_DESCRIPTION,
  UPDATE_PROFILE_IMAGE,
  PROFILE_IMAGE_SUCCESS,
  UPDATE_BACKGROUND_IMAGE,
  LOGIN_WITH_INSTAGRAM,
} from "../../utils/URL";
import { handleError } from "./NetworkAction";
import Toaster from "./../../utils/Toaster/Toast";
import { uploadToS3 } from "./../actions/UploadActions";

import { getProfileImageOriginalUrl } from "../../utils/Utility";

export const signUpUser = (postData, history) => {
  return (dispatch) => {
    dispatch(showSignUpLoading());
    HttpBaseClient.post(SIGNUP, postData, 3)
      .then((response) => {
        dispatch(hideSignUpLoading());
        if (response.code === 0) {
          Toaster.success(response.data);
          let data = {
            email: postData.email,
            password: postData.password,
            device: {},
          };
          dispatch({
            type: Constants.ACTIONS.SET_SIGNUP_USER,
            payload: data,
          });
          history.push("/verify");
        } else if (response.code === 2) {
          Toaster.error(response.message);
        } else {
          Toaster.error(Constants.VALIDATION_MSG.REQ_FAILED);
        }
      })
      .catch((error) => {
        dispatch(hideSignUpLoading());
        // dispatch(handleError(error));
      });
  };
};

//API for Email Verification
export const invokeResendEmail = (postData) => {
  return (dispatch) => {
    HttpBaseClient.post(RESEND_EMAIL, postData, 3)
      .then((response) => {
        if (response.code === 0) {
          Toaster.success(response.data);
        } else {
          Toaster.error(response.message);
        }
      })
      .catch((error) => {
        Toaster.error("Email and Mobile are already verified");
      });
  };
};

//API for Add Bio Description Details
export const addBioDescriptionDetails = (postData, history) => {
  return (dispatch) => {
    HttpBaseClient.post(GET_USER_DESCRIPTION, postData, 1)
      .then((response) => {
        if (response.code === 0) {
          Toaster.success("Bio information added successfully");
          history.push("/connect-social-login");
        }
      })
      .catch((error) => {
        Toaster.error("Bio information  unsuccessful");
      });
  };
};

// save about you details of the user
export const saveAboutYouDetails = (values, callBack) => {
  return (dispatch) => {
    dispatch({
      type: Constants.ACTIONS.SAVE_ABOUT_DETAILS_SUCCESS,
      aboutUserDetails: values,
    });
    callBack(true);
  };
};

export const changeSelection = (index, selectionStatus, id) => {
  return (dispatch, getState) => {
    let changeArray = [...getState().registrationState.interestArray];
    for (let i = 0; i < changeArray.length; i++) {
      if (changeArray[i]._id == id) {
        changeArray[i].is_selected = selectionStatus;
      }
    }
    dispatch({
      type: Constants.ACTIONS.GET_USER_INTERESTS,
      interestArray: changeArray,
    });
  };
};

export const linkInstaAuthFan = (params, callBack) => {
  return (dispatch) => {
    HttpBaseClient.post(LOGIN_WITH_INSTAGRAM, params, 1)
      .then((response) => {
        if (response.code === 0) {
          callBack(true);
        } else {
          callBack(false);
        }
      })
      .catch((error) => {
        dispatch(handleError(error));
      });
  };
};

// API for upload image

export const updateUserProfile = (
  profile_image,
  deviceState,
  isUpdateBackgroundImage
) => {
  return (dispatch) => {
    // dispatch(uploadingInit());
    let splitArr = profile_image[0].type.split("/");
    console.log(splitArr);
    let profile_image_type = splitArr[1];
    HttpBaseClient.post(UPDATE_PROFILE_IMAGE, { profile_image_type }, 1)
      .then((response) => {
        let nameArray = [{ name: response.data.image_name }];
        if (response.code === 0) {
          let mediaTypeArray = [];
          mediaTypeArray.push(3);
          dispatch(
            uploadToS3(
              mediaTypeArray,
              profile_image,
              nameArray,
              response.data.S3,
              0,
              deviceState,
              (isDone) => {
                if (isDone) {
                  dispatch(
                    userProfileSuccess(
                      nameArray[0].name,
                      isUpdateBackgroundImage,
                      deviceState
                    )
                  );
                } else {
                  //dispatch(hideUploadDialog());
                  // Utility.showSnackBar(
                  //   Constants.VALIDATION_MSG.PROFILE_PIC_FAILED,
                  // );
                }
              }
            )
          );
        } else {
          //dispatch(hideUploadDialog());
          // Utility.showAlert(
          //   // Constants.ALERT.TITLE.ERROR,
          //   // Constants.VALIDATION_MSG.REQ_FAILED,
          // );
        }
      })
      .catch((error) => {
        dispatch(handleError(error));
      });
  };
};

export const userProfileSuccess = (
  profile_image,
  isUpdateBackgroundProfile,
  deviceState
) => {
  return (dispatch, callBack) => {
    // dispatch(uploadingFinalizing());

    let updateProfileBackgroundImageUrl = isUpdateBackgroundProfile
      ? UPDATE_BACKGROUND_IMAGE
      : PROFILE_IMAGE_SUCCESS;

    let params = { is_success: true };
    if (isUpdateBackgroundProfile) {
      params.background_image = profile_image;
    } else {
      params.profile_image = profile_image;
    }
    HttpBaseClient.post(updateProfileBackgroundImageUrl, params, 1)
      .then((response) => {
        // dispatch(hideUploadDialog());
        if (response.code === 0) {
          console.log(response.code);
          dispatch({
            type: Constants.ACTIONS.GET_USER_UPLOAD_PROFILE,
            payload: response.data,
          });
          getProfileImageOriginalUrl(
            response.data.background_photo,
            deviceState
          );
          callBack(response);
        } else {
          Toaster.error("Email and Mobile are already verified");
        }
      })
      .catch((error) => {
        dispatch(handleError(error));
      });
  };
};

export const showSignUpLoading = () => {
  return (dispatch) => {
    dispatch({
      type: Constants.ACTIONS.SHOW_SIGN_UP_SCREEN_LOADING,
    });
  };
};

export const hideSignUpLoading = () => {
  return (dispatch) => {
    dispatch({
      type: Constants.ACTIONS.HIDE_SIGN_UP_SCREEN_LOADING,
    });
  };
};
