import React, { useEffect } from "react";
import { connect, useSelector } from "react-redux";
import { Link, useParams } from "react-router-dom";
import moment from "moment";
import ReactPlayer from 'react-player'
// Image
import profileFive from "./../../assets/images/img1(2).png";
import calendar from "./../../assets/images/nav-icon/calendar.svg";
//Common Component
import SideMenu from "./../../components/SideMenu";
import PageLoader from "./../../components/PageLoader";
import ChatBox from "./../../components/ChatBox";
// API Call
import { getCollaborationDetails } from "./../../redux/actions/collaborations/CollaborationAction";
import { getProfileImageOriginalUrl, getPostImageUrl, getPostVideoUrl, getDate } from "./../../utils/Utility";
import { downloandImage } from "../../redux/actions/common/DownloadAction";

export const BrandCollabDetailScreen = (props) => {
  const urlParams = useParams();
  const order_id = urlParams._id;

  useEffect(() => {
    props.getCollaborationDetails(order_id);
  }, [order_id]);

  // From Reducer
  const deviceState = useSelector((state) => state.deviceState);
  const collabortionState = useSelector((state) => state.collabortionState);
  const collabDetailObj = collabortionState.collabDetailObj;
  console.log(collabDetailObj, "++++++++collabDetailObj")

  // this function returns the chat box of particular chat
  const renderChatBox = () => {
    return <ChatBox />;
  };

  const downloandContent = () => {
    if (collabDetailObj && collabDetailObj.media && collabDetailObj.media.length) {
      collabDetailObj.media.forEach(async (value, index) => {
        if (value.is_video) {
          await downloandImage(getPostVideoUrl(value.name, deviceState));

        } else {
          // await downloandImage(getPostImageUrl(value.name, deviceState));

        }
      });
    }
  };

  const renderBrandCollabDetail = () => {
    if (collabortionState.isCollabDetailLoading) {
      return <PageLoader isLoading={collabortionState.isCollabDetailLoading} isLoggedin={true} />
    } else {
      return (
        <div className="container-fluid h-100">
          <div className="row h-100 mobile-overflow">
            <div className="col-md-7 pt-5 pb-3 h-desktop-100">
              <div className="row border-end h-100">
                <div className="col-lg-10 offset-lg-1 h-100">
                  <div className="my-3">
                    <Link to="/brand-collaborations">
                      <i className="fal fa-long-arrow-left fa-2x text-white"></i>
                    </Link>
                    <h1 className="fw-normal mb-0 display-3">collab details</h1>
                  </div>
                  <div className="rounded-2 border-blur bg-blur text-center d-xl-flex mb-3 p-3">
                    <div className="profile-pic align-self-center me-4">
                      <img src={collabDetailObj.ordered_by && collabDetailObj.ordered_by.photo ?
                        getProfileImageOriginalUrl(collabDetailObj.ordered_by.photo, deviceState) :
                        profileFive} alt="" className="img-fluid" />
                    </div>
                    <div className="py-3">
                      <h4 className="mb-0 text-xl-start fw-normal">
                        {collabDetailObj.influencer && collabDetailObj.influencer.first_name && collabDetailObj.influencer.last_name ?
                          collabDetailObj.influencer.first_name + ' ' + collabDetailObj.influencer.last_name :
                          collabDetailObj.influencer && collabDetailObj.influencer.first_name ?
                            collabDetailObj.influencer.first_name :
                            ""
                        }
                      </h4>
                      <div className="d-xl-flex small text-secondary">
                        <i className="align-self-center fab fa-instagram me-1"></i>
                        <small className="align-self-center">
                          {collabDetailObj.service_type ? collabDetailObj.service_type : ""}
                        </small>
                      </div>
                    </div>
                    <div className="ms-auto pe-xl-3 pb-xl-0 pb-3">
                      <p className={`small spacer mb-1 ${collabDetailObj.status === "REQUESTED" ? `text-info` :
                        collabDetailObj.status === "INPROGRESS" ? `text-warning` : `text-success`}`}>
                        {collabDetailObj.status ? collabDetailObj.status : ""}
                      </p>
                      <h4 className="mb-0 fw-normal">
                        ${collabDetailObj.overall_total_amount ? collabDetailObj.overall_total_amount : ""}
                      </h4>
                    </div>
                  </div>
                  <div className="h-100 overflow-auto collab-vertical-align underneath">
                    <div class="row clear-row"></div>
                    <div className="gradient-stepper mt-4">
                      <div className="step-bar" style={{ width: collabDetailObj.status === "COMPLETED" ? "100%" : collabDetailObj.status === "INPROGRESS" ? "50%" : "0%" }}></div>
                      <div className="step">
                        <div>
                          <div className="steps-circle"></div>
                          <p className="mb-0 text-info">Requested</p>
                          <p className="mb-0">
                            {collabDetailObj.ordered_date ?
                              moment(getDate(collabDetailObj.ordered_date)).format("MM/DD/YYYY") :
                              ""
                            }
                          </p>
                        </div>
                        <div className={`text-center ${collabDetailObj.status === "INPROGRESS" || collabDetailObj.status === "COMPLETED" ? `` : `text-secondary`}`}>
                          <div className={`steps-circle m-auto ${collabDetailObj.status === "INPROGRESS" || collabDetailObj.status === "COMPLETED" ? `border-warning` : `border-secondary`}`}></div>
                          <p className={`mb-0 ${collabDetailObj.status === "INPROGRESS" || collabDetailObj.status === "COMPLETED" ? `text-warning` : ``}`}>Accepted</p>
                          <p className="mb-0">
                            {collabDetailObj.inprogress_on ?
                              moment(getDate(collabDetailObj.inprogress_on)).format("MM/DD/YYYY") :
                              ""
                            }
                          </p>
                        </div>
                        <div className={`text-end ${collabDetailObj.status === "COMPLETED" || collabDetailObj.status === "EXPIRED" ? `` : `text-secondary`}`}>
                          <div className={`steps-circle ms-auto ${collabDetailObj.status === "COMPLETED" ? `border-success` : collabDetailObj.status === "EXPIRED" ? `border-danger` : `border-secondary`}`}></div>
                          <p className={`mb-0 ${collabDetailObj.status === "COMPLETED" ? `text-success` : collabDetailObj.status === "EXPIRED" ? `text-danger` : ``}`}>
                            {collabDetailObj.status === "EXPIRED" ? "Expired" : "Completed"}
                          </p>
                          <p className="mb-0">
                            {collabDetailObj.completed_on ?
                              moment(getDate(collabDetailObj.completed_on)).format("MM/DD/YYYY") :
                              collabDetailObj.completed_on ?
                                moment(getDate(collabDetailObj.expired_on)).format("MM/DD/YYYY") :
                                ""
                            }
                          </p>
                        </div>
                      </div>
                    </div>
                    <div>
                      <h5 className="mt-5">instructions</h5>
                      <p className="text-light">{collabDetailObj.description ? collabDetailObj.description : ""}</p>
                    </div>
                    <div>
                      <h5 className="mt-5">due date</h5>
                      <div className="d-flex">
                        <img src={calendar} style={{ width: "18px" }} className="me-2" />
                        <p className="text-light mb-0">{collabDetailObj.expected_date ? moment(getDate(collabDetailObj.expected_date)).format("MM/DD/YYYY") : ""}</p>
                      </div>
                    </div>
                    <div>
                      <h5 className="mt-5 mb-3">attachments
                      {collabDetailObj.media && collabDetailObj.media.length ?
                          <i class="far fa-download ms-2" onClick={() => downloandContent()}></i>
                          :
                          <></>
                        }
                      </h5>
                      <div class="row mb-3 h-100">
                        {collabDetailObj.media && collabDetailObj.media.length
                          ? collabDetailObj.media.map((value, index) => {
                            return (
                              <div className="col-3" key={index}>
                                {value.is_video ?
                                  <ReactPlayer
                                    url={value.name ? getPostVideoUrl(value.name, deviceState) : ""}
                                    className="react-player img-card-collab"
                                    playing={false}
                                    width="100%"
                                    height="100%"
                                  />
                                  :
                                  <img className="img-card-collab" src={
                                    value.name
                                      ? getPostImageUrl(
                                        value.name,
                                        deviceState
                                      )
                                      : ""
                                  } alt="" />
                                }
                              </div>
                            )
                          })
                          :
                          "No attachments"
                        }
                      </div>
                      <div class="row clear-row"></div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            {renderChatBox()}
          </div>
        </div>
      )
    }
  }

  return (
    <div className='w-100 h-100 position-fixed'>
      <div className='d-flex h-100'>
        <SideMenu />
        <div className="w-100">
          {renderBrandCollabDetail()}
        </div>
      </div>
    </div >
  );
};

const mapStateToProps = (state) => ({});

const mapDispatchToProps = {
  getCollaborationDetails
};

export default connect(mapStateToProps, mapDispatchToProps)(BrandCollabDetailScreen);
