export const MessengerScreen = (props) => {
  // rendering the list of chats in messenger screen under chat list section
  const renderChatList = () => {
    const tempArray = [];
    chatListArray.map((item, index) => {
      tempArray.push(
        <div>
          <div
            class={
              item.isActive
                ? "active-circle aligns-self-center"
                : "inactive-circle aligns-self-center"
            }
          ></div>
          <div class="d-flex ms-3 ps-3">
            <div class="d-md-flex py-4 flex-fill bd-highlight border-bottom">
              <div class="messenger-profile align-self-center me-4">
                <img src={image2} alt="" class="img-fluid" />
              </div>
              <div class="align-self-center flex-grow-1">
                <h4 class="mb-1">{item.name}</h4>
                <h5 class="text-secondary mb-0 fw-lighter">{item.message}</h5>
              </div>
              <div class="list-inline">
                <h5 class="text-secondary mb-0 fw-lighter">{item.date}</h5>
              </div>
            </div>
          </div>
        </div>
      );
    });
    return tempArray;
  };

  // this function returns the chat box of particular chat
  const renderChatBox = () => {
    return <ChatBox />;
  };

  return (
    <div class="w-100 h-100 position-fixed">
      <div class="d-flex h-100">
        <SideMenu />
        <div class="w-100">
          <div class="container-fluid h-100">
            <div class="row h-100 mobile-overflow">
              <div class="col-lg-7 pt-5 pb-3 h-desktop-100">
                <div class="row h-100">
                  <div class="col-lg-10 offset-lg-1 h-100">
                    <div class="d-flex mb-3">
                      <div>
                        <h1 class="fw-normal mb-0 display-3">messenger</h1>
                      </div>
                    </div>
                    <div class="bg-blur rounded-3 py-2 px-3">
                      <div class="d-flex">
                        <i class="fal fa-search align-self-center"></i>
                        <input
                          type="text"
                          class="form-control border-0 bg-transparent"
                          placeholder="search conversations"
                        />
                        <button class="btn btn-primary rounded-3 px-4">
                          Search
                        </button>
                      </div>
                    </div>
                    <div class="h-100 mt-3 overflow-auto">
                      {renderChatList()}
                    </div>
                  </div>
                </div>
              </div>
              {renderChatBox()}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

MessengerScreen.propTypes = {
  props: PropTypes,
};

const mapStateToProps = (state) => ({});

const mapDispatchToProps = {};

export default connect(mapStateToProps, mapDispatchToProps)(MessengerScreen);
