import React, { useState } from "react";
import { connect, useSelector } from "react-redux";
import { Link, useHistory } from "react-router-dom";
import { Formik } from "formik";
import * as Yup from "yup";
import { REGEX } from "./../../utils/Constants";
// Image
import envelope from "./../../assets/images/envelope.svg";
import eye from "./../../assets/images/eye.svg";
import eyeopen from "./../../assets/images/eye-visible.svg";
import logo from "./../../assets/images/logo.svg";
import mobile from "./../../assets/images/mobile.svg";
import phone from "./../../assets/images/phone.svg";
import usFlag from "./../../assets/images/us_flag.png";
import indiaFlag from "./../../assets/images/ind_flag.png";
// API Call
import { signUpUser } from "./../../redux/actions/SignupAction";
// Common Component
import ButtonLoader from "../../components/ButtonLoader";

export const SignUpScreen = (props) => {
	const params = {
		email: "",
		password: "",
		countryCode: "+1",
		phoneNumber: "",
		termsConditions: false
	};
	const [showPassword, setShowPassword] = useState(false)
	const countryList = [
		{
			countryName: "United States",
			abbrivation: "US",
			countryCode: "+1",
			countryFlag: usFlag,
		},
		{
			countryName: "India",
			abbrivation: "IN",
			countryCode: "+91",
			countryFlag: indiaFlag,
		},
	];
	const history = useHistory();

	// On Click API call
	const handleSubmit = async (values) => {
		let signUpPostData = {
			email: values.email,
			password: values.password,
			mobile: values.countryCode + values.phoneNumber,
			signup_code: values.referral_code,
		};
		props.signUpUser(signUpPostData, history);
	};

	// Validation
	const formValidationSchema = Yup.object().shape({
		email: Yup
			.string()
			.email("Enter valid email address")
			.matches(REGEX.EMAIL, "Phone number is not valid")
			.required("Email is required"),
		password: Yup
			.string("Invalid password")
			.required("Password is required")
			.matches(REGEX.PASSWORD, "Password is required"),
		phoneNumber: Yup
			.string()
			.required("Phone number is required")
			.matches(REGEX.PHONE, "Phone number is not valid")
			.max(10, "Phone number must be at most 10 digits")
			.min(10, "Phone number must be at most 10 digits"),
		termsConditions: Yup.bool().oneOf([true], "Please accept terms of use")
	});

	// From Reducer
	const signupState = useSelector((state) => state.signupState);

	return (
		<div className="w-100 h-100 position-fixed">
			<div className="w-100 h-100 d-flex overflow-auto">
				<div className="container-fluid">
					<div className="row h-100">
						<div className="col-sm-6">
							<div className="d-flex flex-column h-100">
								<div className="logo">
									<img src={logo} className="mt-3" alt="" />
								</div>
								<div className="row my-auto">
									<div className="col-sm-6 offset-sm-3">
										<h1 className="mb-5 display-3">Sign Up</h1>
										<Formik
											initialValues={{ ...params }}
											validationSchema={formValidationSchema}
											onSubmit={handleSubmit}
											enableReinitialize={true}
										>
											{({
												values,
												touched,
												errors,
												handleSubmit,
												handleChange,
											}) => {
												return (
													<form onSubmit={handleSubmit}>
														<div className="mb-3">
															<label className="form-label">Email</label>
															<div className="input-group flex-nowrap">
																<input
																	name="email"
																	id="email"
																	type="text"
																	className="form-control"
																	placeholder="Enter email address..."
																	onChange={handleChange}
																	value={values.email}
																/>
																<span className="input-group-text">
																	<img src={envelope} alt="" />
																</span>
															</div>
															{errors.email && touched.email ? (
																<div className="error-message">
																	{errors.email}
																</div>
															) : null}
														</div>
														<div className="mb-3">
															<label className="form-label">Password</label>
															<div className="input-group flex-nowrap">
																<input
																	name="password"
																	id="password"
																	type={showPassword ? "text" : "password"}
																	className="form-control"
																	placeholder="Enter at least 8 characters..."
																	onChange={handleChange}
																	value={values.password}
																/>
																<span className="input-group-text" onClick={() => { setShowPassword(!showPassword) }}>
																	<img src={showPassword ? eyeopen : eye} alt="" />
																</span>
															</div>
															{errors.password && touched.password ? (
																<div className="error-message">
																	{errors.password}
																</div>
															) : null}
														</div>
														<div className="row">
															<div className="col-4">
																<div className="mb-3">
																	<label className="form-label">Phone</label>
																	<select
																		name="countryCode"
																		className="form-control h-100 text-white"
																		id="countryCode"
																		onChange={handleChange}
																		value={values.countryCode}
																	>
																		{countryList &&
																			countryList.length &&
																			countryList.map((country, index) => {
																				return (
																					<option value={country.countryCode} key={index}>{country.abbrivation}</option>
																				);
																			})}

																	</select>
																</div>
															</div>
															<div className="col-8">
																<div className="mb-3">
																	<label className="form-label">&nbsp;</label>
																	<div className="input-group flex-nowrap">
																		<input
																			name="phoneNumber"
																			id="phoneNumber"
																			type="text"
																			className="form-control"
																			placeholder="999-999-9999"
																			onChange={handleChange}
																			value={values.phoneNumber}
																		/>
																		<span className="input-group-text">
																			<img src={phone} alt="" />
																		</span>
																	</div>
																	{errors.phoneNumber && touched.phoneNumber ? (
																		<div className="error-message">
																			{errors.phoneNumber}
																		</div>
																	) : null}
																</div>
															</div>
														</div>
														<div className="form-check my-4">
															<input className="form-check-input me-3" type="checkbox" value={values.termsConditions} name="termsConditions" onChange={handleChange} />
															<label className="form-check-label text-white">
																Please accept out <Link to="/">terms of use</Link>
															</label>
															{errors.termsConditions && touched.termsConditions ? (
																<div className="error-message">
																	{errors.termsConditions}
																</div>
															) : null}
														</div>
														<button
															className={signupState.isSingUpPageLoading ? "btn btn-primary w-100 disabled" : "btn btn-primary w-100"}
															type="submit"
														>
															{signupState.isSingUpPageLoading ?
																<ButtonLoader /> :
																"create my account!"
															}
														</button>
														<div className="text-center">
															<p className="mb-2 mt-3">Already have an account? <Link to="/">Login</Link></p>
														</div>
													</form>
												)
											}}
										</Formik>
									</div>
								</div>
							</div>
						</div>
						<div className="col-sm-6 login-bg">
							<div className="row h-100">
								<div className="col-sm-10 offset-sm-1 align-self-center">
									<img className="img-fluid" src={mobile} alt="" />
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	);
};

const mapStateToProps = (state) => ({});

const mapDispatchToProps = {
	signUpUser,
};

export default connect(mapStateToProps, mapDispatchToProps)(SignUpScreen);