 import AWS from "aws-sdk";
 import Constants from "./../../utils/Constants";
 import Toaster from "./../../utils/Toaster/Toast";

 export const uploadToS3 = (
   media_type_arr,
   filePathArray,
   fileNameArray,
   S3Config,
   position,
   deviceState,
   callback
 ) => {
   return async (dispatch) => {
     dispatch(uploadingStarted(fileNameArray.length, position + 1));
 
     AWS.config.update({
       secretAccessKey: S3Config.SecretAccessKey,
       accessKeyId: S3Config.AccessKeyId,
       sessionToken: S3Config.SessionToken,
     });
     let location = deviceState.profileImageLocation;
     const media_type = media_type_arr[position];
     if (media_type === 1) {
       location = deviceState.s3postImageLocation;
     } else if (media_type === 2) {
       location = deviceState.s3postVideoLocation;
     } else if (media_type === 3) {
       location = deviceState.profileImageLocation;
     } else if (media_type === 4) {
       location = deviceState.s3storyImageLocation;
     } else if (media_type === 5) {
       location = deviceState.s3campaignImageLocation;
     } else if (media_type === 6) {
       location = deviceState.s3documentLoaction;
     }
     // Create S3 service object
     let s3obj = new AWS.S3({
       params: {
         Bucket:
           media_type === 2
             ? deviceState.s3videoBucketName
             : deviceState.s3ImageBucketName,
         Key: location + fileNameArray[position].name,
       },
       options: {
         queueSize: 1,
       },
       httpOptions: {
         timeout: 0,
       },
       apiVersion: '2006-03-01',
       maxRetries: 10,
       signatureVersion: 'v4',
       region: 'us-west-2',
       // options: { partSize: 10 * 1024 * 1024, queueSize: 3 }
     });
    
 
    await s3obj
       .upload({
         Body: filePathArray[position],
       })
       .on('httpUploadProgress', function (evt) {
         let percentComplete = (evt.loaded / evt.total).toFixed(2) * 100;
         percentComplete = percentComplete.toFixed(0);
         console.log('-=-=-=-uploadper-=-=', percentComplete);
         dispatch(updateUploadProgress(percentComplete));
       })
       .send(function (err, data) {
         if (err) {
           callback(false);
         } else {
           if (fileNameArray.length > position + 1) {
             dispatch(
               uploadToS3(
                 media_type_arr,
                 filePathArray,
                 fileNameArray,
                 S3Config,
                 position + 1,
                 deviceState,
                 callback,
               ),
             );
           } else {
             dispatch(uploadingFinalizing());
             callback(true);
           }
         }
       });
 
    
     
 };
 }
 export const uploadingStarted = (totalCount, activeCount) => ({
   type: Constants.ACTIONS.UPLOADING_STARTED,
   totalCount,
   activeCount,
 });
 
 export const uploadingFinalizing = () => ({
   type: Constants.ACTIONS.UPLOADING_FINALIZING,
 });
 
 export const uploadingCompleted = () => ({
   type: Constants.ACTIONS.UPLOADING_COMPLETED,
 });
 
 export const updateTotalCount = (totalCount) => ({
   type: Constants.ACTIONS.UPDATE_TOTAL_COUNT,
   totalCount,
 });
 
 export const updateCurrentCount = (activeCount) => ({
   type: Constants.ACTIONS.UPDATE_CURRENT_COUNT,
   activeCount,
 });
 
 export const updateUploadProgress = (progress) => ({
   type: Constants.ACTIONS.UPDATE_UPLOAD_PROGRESS,
   progress,
 });