import React from "react";
import Routes from "./app/routes";
import { Provider } from "react-redux";
import store from "../src/app/redux/store";
import Toaster from "./../src/app/utils/Toaster";


const App = () => {
  return (
    <Provider store={store}>
      <Toaster />
      <Routes />
    </Provider>
  );
};

export default App;
